import Timestamp from './Timestamp.js';
import React from 'react';
import getInternURL from './getInternURL.js';
import JoinMeetingButtonV2 from './JoinMeetingButtonV2.js';
import OneOneButtonV2 from './OneOneButtonV2.js';
import Tippy from '@tippyjs/react';
import Linkify from 'react-linkify';
import CalendarDocumentList from './CalendarDocumentList.js';
import genLog from './genLog.js';

function EventPopoverV2(props) {
  const start = new Date(props.event.start_time * 1000);
  const end = new Date(props.event.end_time * 1000);
  const inviteeCount = props.event.attendees.count;
  const invitees = props.event.attendees.edges.map((a, idx) => {
    const pic = a.node.profile_picture
      ? a.node.profile_picture.uri
      : 'silhouette.png';
    const id = a.node.employee ? a.node.employee.unencoded_id : null;
    if (id != null) {
      return (
        <Tippy content={a.node.employee.full_name} placement="top" key={id}>
          <a
            className="eventPopoverAttendeeV2"
            href={getInternURL('/profile/?id=' + id)}
          >
            <img src={pic} alt="Event Attendee" />
          </a>
        </Tippy>
      );
    }
    return (
      <Tippy content={a.node.email} placement="top" key={idx}>
        <div className="eventPopoverAttendee">
          <img src={pic} alt="Event Attendee" />
        </div>
      </Tippy>
    );
  });
  invitees.reverse();
  const uri = props.event.uri || props.event.edit_uri;
  const description = props.event.notes_rte?.rte_content_plain_text;
  const rooms = props.event.rooms || [];

  return (
    <div className="eventPopoverV2">
      <div className="eventPopoverHeader">
        <div className="eventPopoverTitleV2">
          {uri != null ? (
            <a
              href={uri}
              onClick={() => genLog('click_individual_meeting')}
              style={{
                color: 'var(--primary-color)',
              }}
            >
              {props.event.title}
            </a>
          ) : (
            props.event.title
          )}
        </div>
        <div className="eventPopoverTimeV2">
          <Timestamp time={start} />
          {' - '}
          <Timestamp time={end} />
          <div className="eventPopoverLocationV2">
            {rooms.map((r, i) => (
              <div key={'room' + i}>
                {i !== 0 && <span className="meetingRoomDivider">|</span>}
                <a
                  className="eventPopoverMeetingRoomLink"
                  href={getInternURL('/room_profile/?id=' + r.room.id)}
                  onClick={() => genLog('click_meeting_location')}
                >
                  {r.display_name}
                </a>
              </div>
            ))}
            {(props.event.freeform_locations || []).map((f, idx) => (
              <div key={idx}>
                {(idx !== 0 || rooms.length !== 0) && (
                  <span className="meetingRoomDivider">|</span>
                )}
                {f}
              </div>
            ))}
          </div>
        </div>
        {inviteeCount > 0 ? (
          <div className="eventPopoverAttendeesV2">
            {invitees}
            {invitees.length < inviteeCount ? (
              <div className="eventPopoverAttendeeExtraV2">
                {'+' + String(inviteeCount - invitees.length)}
              </div>
            ) : null}
          </div>
        ) : null}
      </div>
      {description ? (
        <div className="eventPopoverDescriptionContainerV2">
          <div className="eventPopoverDescriptionV2">
            <Linkify>{description}</Linkify>
          </div>
        </div>
      ) : null}
      {((props.event.knowledge_links ?? []).length !== 0 ||
        props.event.conference != null) && (
        <div className="calendarDocumentListCTAContainer">
          <CalendarDocumentList event={props.event} />
          <OneOneButtonV2 event={props.event} />
          <JoinMeetingButtonV2 event={props.event} />
        </div>
      )}
    </div>
  );
}

export default EventPopoverV2;
