const ResultTitle = styled.div`
  font-size: 14px;
  font-family: SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif;
  font-weight: 400;
  line-height: 20px;
`;

const SEARCH_SUGGESTIONS_LIMIT = 10;

function typeaheadClickLog(
  entry: string,
  query: string,
  entryType: string,
  rank: number
) {
  const logData = {
    typeahead_input_query: query,
    query: entry.main,
    rank: rank,
    document_type: entry.type,
    doc_uri: entry.link,
  };
  connectedSearchLog({
    event: InternSearchInteractionEvent.TYPEAHEAD_SUGGESTION,
    ...logData,
  });
  connectedSearchLog({
    event: InternSearchInteractionEvent.TYPEAHEAD_SUGGESTION_SELECT,
    ...logData,
  });
}

function goToInternSearch(text: string) {
  const sessionId = window.sessionStorage.getItem(storageKeys.sessionId);
  const url = `https://www.internalfb.com/search?ref=intern_new_tab&uber_session_id=${sessionId}&query=${encodeURIComponent(
    text
  )}`;
  window.location.href = url;
}

export default function Search() {
  const [text, setText] = React.useState('');
  const [showSuggestions, setShowSuggestions] = React.useState(false);
  const [searchHistory, setSearchHistory] = React.useState([]);
  const [searchSuggestions, setSearchSuggestions] = React.useState([]);
  const [searchEnabled] = useStateWithSyncStorage(storageKeys.search, true);

  const [goToBunnyWithEnter] = useStateWithSyncStorage(
    storageKeys.goToBunnyWithEnter,
    false
  );

  const bunny = getDescription(text.replace(/ .+/, '').trim());
  const bunnyURL = getInternURL('/bunny/?q=' + encodeURIComponent(text));

  React.useEffect(() => {
    setSearchHistory([]);
    setSearchSuggestions([]);
    genCachedGraphQL(
      INTERN_SEARCH_SUGGESTION_QUERY,
      {
        query: text,
        surface_caller: 'INTERN_SEARCH_HOME',
        suggestion_type: 'recent_search',
      },
      3000,
      data => setSearchHistory(data.response)
    );
    if (text.length === 0) {
      setSearchSuggestions([]);
    } else {
      genLog('search_fetch', text);
      genCachedGraphQL(
        INTERN_SEARCH_SUGGESTION_QUERY,
        {
          query: text,
          surface_caller: 'INTERN_NEW_TAB',
          suggestion_type: 'query',
        },
        3000,
        data => setSearchSuggestions(data.response)
      );
    }
  }, [text]);

  if (searchEnabled === false) {
    return null;
  }

  // If there are suggestions to show, truncate history to make room
  const numHistoryToShow = Math.max(SEARCH_SUGGESTIONS_LIMIT / 2, SEARCH_SUGGESTIONS_LIMIT - searchSuggestions.length);

  const results = searchHistory
    .slice(0, numHistoryToShow)
    .concat(searchSuggestions)
    .slice(0, SEARCH_SUGGESTIONS_LIMIT);

  return (
    <div className={classNames({searchWrapper: true})}>
      {text !== '' && (
        <div className="searchCloseButtonV2">
          <CloseButton
            onClick={() => {
              setText('');
            }}
          />
        </div>
      )}
      <div
        className={classNames({
          searchWithSuggestions: true,
        })}
      >
        <SearchInputWrapper>
          <SearchInput
            type="text"
            value={text}
            placeholder="Search Meta"
            onChange={e => {
              setText(e.target.value);
            }}
            onFocus={() => setShowSuggestions(true)}
            onBlur={() => setShowSuggestions(false)}
            onKeyUp={e => {
              if (e.keyCode === 13) {
                // enter
                if (bunny && goToBunnyWithEnter) {
                  genLog('bunny_enter', text);
                  window.location.href = bunnyURL;
                } else {
                  genLog('search_enter', text);
                  goToInternSearch(text);
                }
              } else if (e.keyCode === 27) {
                // escape
                setText('');
                e.target.blur();
                genLog('search_escape');
              }
            }}
            role="combobox"
            aria-expanded={text != null && text !== ''}
            aria-haspopup={true}
          />
          <SearchInputIcon src="/search-icon.png" />
        </SearchInputWrapper>
        {showSuggestions && results.length > 0 && (
          <SuggestionList>
            {results.map((node, rank) => (
              <Suggestion
                href="#"
                key={rank}
                onMouseDown={() => {
                  setText(node.title);
                  genLog('suggestion_click', node.title);
                  goToInternSearch(node.title);
                }}
              >
                {node.sourceType === 'RECENT_SEARCH' ? (
                  <SearchIcon src="/clock.png" />
                ) : (
                  <SearchSuggestionIcon src="/search-icon.png" />
                )}
                <ResultTitle>{node.title}</ResultTitle>
              </Suggestion>
            ))}
          </SuggestionList>
        )}
      </div>
    </div>
  );
}
