import {useEffect, useState} from 'react';
import {genCachedGraphQL} from './GraphQL.js';

const cache = {};

export default function useGK(gk_name, defaultValue = false) {
  const cachedValue = cache[gk_name];
  const [passes, setPasses] = useState(cachedValue ?? defaultValue);
  useEffect(() => {
    genCachedGraphQL('2150285835052187', {gk_name}, 3600, data => {
      setPasses(!!data.intern_new_tab_gk_check.result);
      cache[gk_name] = !!data.intern_new_tab_gk_check.result;
    });
  }, [gk_name]);
  return passes;
}
