import React from 'react';
import Tippy from '@tippyjs/react';
import styled from 'styled-components';
import ThemedTooltip from './ThemedTooltip.js';
import useGK from './useGK.js';

const Button = styled.button`
  border-radius: 50%;
  border: 0;
  cursor: pointer;
  padding: 8px 8px 6px;
  outline: 0;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
  width: 32px;
  height: 32px;
  box-sizing: border-box;
  background: var(--overlay-background);
`;

const ButtonIcon = styled.img`
  width: 16px;
  height: 16px;
  filter: var(--black-icon-filter);
`;

function RightPaneButton(props) {
  const isRevamp = useGK('intern_new_tab_revamp');

  if (isRevamp) {
    return (
      <ThemedTooltip title={props.label} placement="left">
        <Button className={props.className} onClick={props.onClick}>
          <ButtonIcon src={props.icon} />
        </Button>
      </ThemedTooltip>
    );
  }
  return (
    <Tippy content={props.label}>
      <Button className={props.className} onClick={props.onClick}>
        <ButtonIcon src={props.icon} />
      </Button>
    </Tippy>
  );
}

export default RightPaneButton;
