import React from 'react';
import {ToastContainer} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import Diffs from './diff/Diffs.js';
import BookmarkBar from './BookmarkBar.js';
import Calendar from './Calendar.js';
import Clocks from './Clocks.js';
import EarlyMeeting from './EarlyMeeting.js';
import ErrorMessage from './ErrorMessage.js';
import Greeting from './Greeting.js';
import LibraryDocuments, {
  libraryTabs,
} from './libraryRecentlyViewed/LibraryDocuments.js';
import LeftNav from './LeftNav.js';
import Search from './Search.js';
import SearchV2 from './SearchV2.js';
import Time from './Time.js';
import UpNext from './UpNext.js';
import RightPane from './RightPane.js';
import genCalendar from './genCalendar.js';
import genLog from './genLog.js';
import getInternURL from './getInternURL.js';
import Pixelcloud from './Pixelcloud.js';
import {cleanup} from './GraphQL.js';
import TipsTricks from './TipsTricks.js';
import InternDown from './InternDown.js';
import useIsInternDown from './useIsInternDown.js';
import {useState, useEffect} from 'react';
import {storageKeys, genSet} from './LocalStorage.js';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {genAll} from './SyncStorage.js';
import HttpCacheProvider from './HttpCacheProvider.js';
import BackgroundImageAttribution from './BackgroundImageAttribution.react';
import Metamate from './Metamate.js';
import useGK from './useGK.js';
import Timestamp from './Timestamp.js';
import getDayLabel from './getDayLabel.js';

import './App.css';
import 'tippy.js/dist/tippy.css';

function sameDay(d1, d2) {
  return (
    d1.getFullYear() === d2.getFullYear() &&
    d1.getMonth() === d2.getMonth() &&
    d1.getDate() === d2.getDate()
  );
}
function _getEvents(sources) {
  let flatEvents = [];
  (sources.intern_calendar_event_sources ?? []).forEach(source => {
    source.events.forEach(event => {
      // Skip canceled events
      if (event.is_canceled) {
        return;
      }
      // Skip all day events
      if (event.is_all_day_event) {
        return;
      }
      // Skip Blocks w/ 1 attendee and no rooms
      if (
        source.label === 'Block' &&
        event.attendees.length < 2 &&
        (event.rooms == null || event.rooms.length === 0)
      ) {
        return;
      }
      event.color = source.color;
      flatEvents.push(event);
    });
  });
  flatEvents.sort((a, b) => {
    if (a.start_time !== b.start_time) {
      return a.start_time - b.start_time;
    }
    if (a.is_tentative !== b.is_tentative) {
      if (a.is_tentative) {
        return 1;
      }
      return -1;
    }
    return 0;
  });
  return flatEvents;
}

function _getStartEnd(date) {
  let start = new Date(date);
  start.setHours(0);
  start.setMinutes(0);
  start.setSeconds(0);
  start = Math.floor(start / 1000);
  let end = new Date(date);
  end.setHours(23);
  end.setMinutes(59);
  end.setSeconds(59);
  end = Math.floor(end / 1000);
  return [start, end];
}

function App() {
  const [internmc, setInternMC] = useState(true);
  const [now, setNow] = useState(new Date());
  const [customNow, setCustomNow] = useState(null);
  const [start, setStart] = useState(null);
  const [end, setEnd] = useState(null);
  const [sources, setSources] = useState([]);
  const [futureSources, setFutureSources] = useState([]);
  const [activeLibraryTab, setActiveLibraryTab] = useState(libraryTabs[1]);
  const isRevamp = useGK('intern_new_tab_revamp');

  const isInternDown = useIsInternDown();

  const [calendarEnabled] = useStateWithSyncStorage(storageKeys.calendar, true);
  const [calendarFullDay] = useStateWithSyncStorage(
    storageKeys.calendarFullDay,
    false
  );

  function _getCustomNow() {
    if (customNow == null && now.getHours() >= 19 && !calendarFullDay) {
      // show next day
      const newCustomNow = new Date(now);
      newCustomNow.setDate(newCustomNow.getDate() + 1);
      return newCustomNow;
    }
    return customNow || now;
  }

  const _update = maybeCustomNow => {
    const localNow = new Date();
    const localCustomNow = maybeCustomNow || customNow || localNow;
    const [start, end] = _getStartEnd(localCustomNow);
    if (localCustomNow !== customNow) {
      setCustomNow(localCustomNow);
    }
    setNow(localNow);
    setStart(start);
    setEnd(end);
    genCalendar(start, end, setSources);
    const date = new Date(start * 1000);
    // Preload nearby days together
    const promises = [];
    const DAYS_BEFORE = 1;
    const DAYS_AFTER = 1;
    for (let i = -DAYS_BEFORE; i <= DAYS_AFTER; ++i) {
      if (i === 0) {
        continue;
      }
      const thisDate = new Date(date);
      thisDate.setDate(thisDate.getDate() + i);
      const [start, end] = _getStartEnd(thisDate);
      if (i > 0) {
        promises.push(new Promise(resolve => genCalendar(start, end, resolve)));
      } else {
        genCalendar(start, end, () => {});
      }
    }
    Promise.all(promises).then(setFutureSources);
  };

  async function _checkInternMC() {
    const response = await fetch(getInternURL('/auth/check?json=true'));

    // Invalid response
    if (response.status !== 200) {
      setInternMC(false);
      return;
    }
    const text = await response.text();
    if (text == null) {
      setInternMC(false);
      return;
    }

    // Check result
    const result = JSON.parse(text.slice(9));
    const corpIP = result.is_corp_ip;
    const passed = result.response.indexOf('pass') === 0;
    setInternMC(corpIP || passed);
  }

  async function _captureSettings() {
    genAll().then(data => {
      genLog('chrome_storage_load', JSON.stringify({data}));
    });
  }

  useEffect(() => {
    window.onload = () => {
      const loadingTime =
        window.performance.timing.domContentLoadedEventEnd -
        window.performance.timing.navigationStart;
      window.sessionStorage.setItem(storageKeys.sessionId, crypto.randomUUID());
      genLog('load', loadingTime);
      _captureSettings();
    };
    _checkInternMC();
    if (performance.navigation.type === 1) {
      // synchronous clear on refresh
      cleanup(true).then(() => _update(customNow));
    } else {
      _update(customNow);
      cleanup(false);
    }
  }, []);

  useEffect(() => {
    const id = setInterval(() => {
      _update(customNow);
    }, 5000);
    return () => clearInterval(id);
  }, [customNow]);

  if (!internmc) {
    return (
      <div className="wrapper">
        <ErrorMessage title="Intern New Tab requires InternMC!">
          <a href="https://fburl.com/intern-new-tab-internmc-onboard">
            Opt-in to InternMC
          </a>{' '}
          to use.
        </ErrorMessage>
      </div>
    );
  }

  const todayEvents = _getEvents(sources);
  let upNext = todayEvents.find(e => e.end_time > now / 1000);
  if (upNext == null) {
    // Check future sources
    for (let futureSource of futureSources) {
      const futureEvents = _getEvents(futureSource);
      upNext = futureEvents.find(e => e.start_time > now / 1000);
      if (upNext != null) {
        break;
      }
    }
  }

  const computedCustomNow = _getCustomNow();
  let earlyMeeting = null;
  for (let source of [sources, ...futureSources]) {
    earlyMeeting = EarlyMeeting.find(computedCustomNow, _getEvents(source));
    if (earlyMeeting) {
      break;
    }
  }
  const todayStatus = sources.viewer_intern_user?.status_event_for_time;

  // TODO: Remove the div with style fontFamily and move add fontFamily to body css App.css
  if (isRevamp) {
    return (
      <HttpCacheProvider>
        <BookmarkBar />
        <div className="headerV2">
          <Timestamp time={now} /> {'\u2027'} {getDayLabel(now)}
        </div>
        <div className="mainContent">
          <div className="topSpacing" />
          {isInternDown && <InternDown />}
          <Greeting />
          {isRevamp ? <SearchV2 /> : <Search />}
          {calendarEnabled && (
            <div className="calendarUpNextWrapperV2">
              <Calendar
                now={now}
                customNow={computedCustomNow}
                hideCurrentTime={!sameDay(computedCustomNow, now)}
                status={todayStatus}
                events={todayEvents}
                start={start}
                end={end}
                onNext={() => {
                  const newCustomNow = new Date(computedCustomNow);
                  newCustomNow.setDate(newCustomNow.getDate() + 1);
                  _update(newCustomNow);
                }}
                onPrevious={() => {
                  const newCustomNow = new Date(computedCustomNow);
                  newCustomNow.setDate(newCustomNow.getDate() - 1);
                  _update(newCustomNow);
                }}
              />
              <UpNext
                event={upNext}
                shownDate={computedCustomNow}
                setActiveLibraryTab={setActiveLibraryTab}
              />
            </div>
          )}
          <Diffs />
          <LibraryDocuments
            events={todayEvents}
            upNext={upNext}
            activeLibraryTab={activeLibraryTab}
            setActiveLibraryTab={setActiveLibraryTab}
          />
          <div className="bottomSpacing" />
        </div>
        <RightPane />
        <Metamate />
        <Clocks now={now} />
        <LeftNav />
        <Pixelcloud />
        <ToastContainer
          position="top-right"
          autoClose={3000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
      </HttpCacheProvider>
    );
  }

  return (
    <HttpCacheProvider>
      <div className="wrapper">
        <div className="header">
          {isInternDown && <InternDown />}
          <Time now={now} useDayLabel={true} />
          <Time now={now} />
          <Search />
        </div>
        <div className="calendarUpNextWrapper">
          <TipsTricks />
          <div>
            <EarlyMeeting event={earlyMeeting} />
            <Calendar
              now={now}
              customNow={computedCustomNow}
              hideCurrentTime={!sameDay(computedCustomNow, now)}
              status={todayStatus}
              events={todayEvents}
              start={start}
              end={end}
              onNext={() => {
                const newCustomNow = new Date(computedCustomNow);
                newCustomNow.setDate(newCustomNow.getDate() + 1);
                _update(newCustomNow);
              }}
              onPrevious={() => {
                const newCustomNow = new Date(computedCustomNow);
                newCustomNow.setDate(newCustomNow.getDate() - 1);
                _update(newCustomNow);
              }}
            />
            <UpNext event={upNext} setActiveLibraryTab={setActiveLibraryTab} />
          </div>
          <Diffs />
          <LibraryDocuments
            events={todayEvents}
            upNext={upNext}
            activeLibraryTab={activeLibraryTab}
            setActiveLibraryTab={setActiveLibraryTab}
          />
        </div>
        <RightPane />
        <BookmarkBar />
        <Metamate />
        <Clocks now={now} />
        <LeftNav />
        <Pixelcloud />
        <ToastContainer
          position="top-right"
          autoClose={3000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
        <BackgroundImageAttribution />
      </div>
    </HttpCacheProvider>
  );
}

export default App;
