export default function Metamate(props) {
  const isRevamp = useGK('intern_new_tab_revamp');
  const [isOpen, setIsOpen_] = React.useState(false);
  const videoRef = React.useRef();
  const setIsOpen = React.useCallback(
    newValue => {
      if (newValue) {
        if (videoRef.current != null) {
          videoRef.current.currentTime = 0;
          videoRef.current.play();
        }
      }
      setIsOpen_(newValue);
    },
    [setIsOpen_]
  );
  const [metamateEnabled, setMetamateCta] = useStateWithSyncStorage(
    storageKeys.metamateCta,
    true
  );
  const {hasMetamate, openMetamate} = useMetamate();

  if (!metamateEnabled || !hasMetamate) {
    return null;
  }

  const MetamateWrapperComponent = isRevamp
    ? MetamateWrapperV2
    : MetamateWrapper;

  return (
    <MetamateWrapperComponent>
      <Tippy
        visible={isOpen}
        onClickOutside={() => setIsOpen(false)}
        className="tippyPopover"
        placement="left-start"
        arrow={false}
        interactive={false}
        content={
          <Menu>
            <MenuHeader>
              <MenuHeaderLabel>Pin Metamate to your toolbar</MenuHeaderLabel>
            </MenuHeader>
            <video
              width="100%"
              src="/pin-metamate.mov"
              ref={videoRef}
              loop={true}
            />
          </Menu>
        }>
        <IconWrapper
          onMouseEnter={() => setIsOpen(true)}
          onMouseLeave={() => setIsOpen(false)}
          onClick={() => openMetamate()}>
          <MetamateIcon size={20} />
          Try Metamate
          <br />
          for Chrome{' '}
          <CloseButton
            onClick={() => {
              setMetamateCta(false);
            }}
          />
        </IconWrapper>
      </Tippy>
    </MetamateWrapperComponent>
  );
}
