import React, {memo} from 'react';
import styled from 'styled-components';
import {genCachedGraphQL} from './../GraphQL.js';
import Table from '@mui/material/Table';
import TableContainer from '@mui/material/TableContainer';
import Paper from '@mui/material/Paper';
import DocumentTable from './DocumentTable.js';
import useGK from '.././useGK.js';
import Spinner from '../Spinner.js';
import useStateWithSyncStorage from '../useStateWithSyncStorage.js';
import {storageKeys} from '../LocalStorage.js';

import {
  PERSISTED_QUERY_ID,
  getSourceIcon,
} from './libraryRecentlyViewed.utils.js';
import {useLibraryEnabled} from './libraryRecentlyViewed.hooks.js';

const INTERN_PREFIX = 'https://www.internalfb.com/';

export const CopyLinkIcon = styled.img`
  height: 15px;
  width: 15px;
  filter: var(--icon-filter);
`;

function LibraryRecentlyViewed() {
  const isRevamp = useGK('intern_new_tab_revamp');
  const [theme] = useStateWithSyncStorage(storageKeys.theme);
  const [recentlyViewed, setRecentlyViewed] = React.useState(null);
  const libraryEnabled = useLibraryEnabled();

  const GRAPHQL_CACHE_TTL = 3000;

  React.useEffect(() => {
    genCachedGraphQL(PERSISTED_QUERY_ID, null, GRAPHQL_CACHE_TTL, data => {
      const idSet = new Set();
      const userId = data?.xfb_knowledge_data_viewer?.user_id;
      const viewedDocuments =
        data?.xfb_knowledge_data_viewer?.recent_documents
          ?.map(libraryDoc => {
            const id = libraryDoc.document.id;
            const source = getSourceIcon(
              libraryDoc?.document?.object_id?.source,
              libraryDoc?.document?.type,
              theme
            );
            // TODO: dedupe and add prefix on backend
            const url =
              source === 'KNOWLEDGE_BASE'
                ? INTERN_PREFIX + libraryDoc?.document?.url
                : libraryDoc?.document?.url;

            if (idSet.has(id)) {
              return null;
            }
            idSet.add(id);
            if (url === null || url.trim() === '') {
              return null;
            }

            const doc = libraryDoc?.document;
            return {
              id: doc?.id,
              userId,
              title: libraryDoc?.document?.title ?? 'Untitled',
              viewedTime: doc?.viewed_time,
              url,
              source,
              nativeId: doc?.object_id?.native_id,
              modifiedBy: doc?.last_modified_by,
              modifiedTime: doc?.last_modified_time,
              comments: doc?.comments,
              mentions: doc?.mentions,
              mentionsInComments: doc?.mentions_in_comments,
              docSource: doc?.object_id?.source,
            };
          })
          .filter(viewedDoc => viewedDoc != null)
          .sort((a, b) => {
            return b.viewedTime - a.viewedTime;
          }) ?? [];
      setRecentlyViewed(viewedDocuments);
    });
  }, []);

  if (libraryEnabled === false) {
    return null;
  }

  if (recentlyViewed === null) {
    return <Spinner />;
  }

  return (
    recentlyViewed.length > 0 && (
      <div className="libraryListWrapper">
        <TableContainer
          component={Paper}
          sx={
            isRevamp
              ? {
                  background: 'var(--widget-background-color)',
                  boxShadow: 'none',
                }
              : {background: 'var(--overlay-background)'}
          }
        >
          <Table
            aria-label="recent documents"
            sx={
              isRevamp
                ? {
                    tableLayout: 'fixed',
                    '& .MuiTableRow-root:hover': {
                      backgroundColor: 'var(--hover-overlay-color)',
                    },
                    '& .MuiTableRow-root:active': {
                      backgroundColor: 'var(--focus-overlay-color)',
                    },
                  }
                : {
                    '& .MuiTableRow-root:hover': {
                      backgroundColor: 'var(--highlight-background)',
                    },
                  }
            }
          >
            <DocumentTable
              documents={recentlyViewed}
              referringTab="RECENTLY_VIEWED_TAB"
            />
          </Table>
        </TableContainer>
      </div>
    )
  );
}

export default memo(LibraryRecentlyViewed);
