import styled from 'styled-components';
import React, {useState, useEffect} from 'react';
import genLog from './genLog.js';
import genJoinInVRLink from './genJoinInVRLink.js';
import useGK from './useGK.js';

const ButtonGroup = styled.div`
  display: grid;
  grid-column-gap: 8px;
  grid-template-columns: auto auto;
  margin-top: 8px;
`;

const Button = styled.a`
  display: flex;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 8px 12px;
  border-radius: 6px;
  border: 2px solid var(--divider-color);
  &:hover {
    background-color: rgba(255, 255, 255, 0.1);
    text-decoration: none;
  }
`;

export default function JoinMeetingButton(props) {
  const conference = props.event.conference;

  const conferenceLink = conference ? conference.joinable_link : null;

  const [joinInVRLink, setJoinInVRLink] = useState(conferenceLink);

  const shouldIncludeJoinInVRLink =
    conference?.mode === 'WORKROOMS' || conference?.mode === 'ZOOM';

  const showJoinInVR = useGK('workrooms_join_in_vr_intern_tab');

  useEffect(() => {
    genJoinInVRLink(
      conferenceLink,
      shouldIncludeJoinInVRLink,
      'INTERN_TAB_MEETING_POPOVER',
      setJoinInVRLink
    );
  }, [conferenceLink, shouldIncludeJoinInVRLink]);

  if (conferenceLink == null) {
    return null;
  }

  return (
    <>
      <ButtonGroup>
        {conferenceLink ? (
          <Button
            href={conferenceLink}
            onClick={() => genLog('click_join_meeting')}
          >
            Join Meeting
          </Button>
        ) : null}
        {joinInVRLink && showJoinInVR ? (
          <Button
            href={joinInVRLink}
            onClick={() => genLog('click_join_vr_meeting')}
          >
            Join in VR
          </Button>
        ) : null}
      </ButtonGroup>
    </>
  );
}
