import React from 'react';
import styled from 'styled-components';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';
import genLog from '../genLog.js';
import useGK from '../useGK.js';

const DIFFS_DASHBOARD_URL =
  'https://www.internalfb.com/diffs?entry_point=INTERN_NEW_TAB';

const EmptyResultContainer = styled.div`
  display: flex;
  align-items: center;
  margin-top: 4px;
`;

export default function EmptyResultMessage() {
  const isRevamp = useGK('intern_new_tab_revamp');
  return (
    <EmptyResultContainer>
      <Typography
        variant="caption"
        sx={isRevamp ? {
          color: 'var(--secondary-text-color)',
          fontFamily: 'SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif',
          margin: 'auto',
          paddingTop: '16px',
          paddingBottom: '16px',
        } : {
          color: 'var(--secondary-color)',
          margin: 'auto',
          fontStyle: 'italic',
        }}
      >
        No diffs in this queue at the moment. Go to your{' '}
        <Link
          href={DIFFS_DASHBOARD_URL}
          onClick={() =>
            genLog('diffs_dashboard_link_click', 'empty_result_message_link')
          }
        >
          Diffs Dashboard
        </Link>{' '}
        for more!
      </Typography>
    </EmptyResultContainer>
  );
}
