import React, {useEffect, useState} from 'react';
import useGK from './useGK.js';
import styled, {css} from 'styled-components';
import classNames from 'classnames';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys, DEFAULT_24_HOUR} from './LocalStorage.js';
import useCurrentUser from './useCurrentUser.js';
import CloseButton from './CloseButton.js';
import getInternURL from './getInternURL.js';
import Switch from './Switch.js';
import Tippy from '@tippyjs/react';
import {genGraphQL} from './GraphQL.js';
import genLog from './genLog.js';
import useDiffsEnabled from './diff/useDiffsEnabled.js';

const SettingsButton = styled.button`
  background: transparent;
  border-radius: 50%;
  border: 0;
  cursor: pointer;
  padding: 8px 8px 6px;
  outline: 0;
  opacity: 0.9;
  &:hover {
    opacity: 1;
  }
`;

const SettingsButtonImage = styled.img`
  width: 18px;
  height: 18px;
  filter: var(--icon-filter);
`;

const SettingsWrapper = styled.div`
  position: absolute;
  color: var(--primary-color);
  &:hover {
    opacity: 1;
    background-color: var(--overlay-background);
  }
  background-color: var(--overlay-background-opaque);
  border-radius: 10%;
  margin: 16px 8px;
  left: 0;
  bottom: 0;
  ${props =>
    props.hasAttribution &&
    css`
      left: 0;
      bottom: 60px;
    `};
`;

const SettingsMenu = styled.div`
  background: var(--overlay-background);
  color: var(--primary-color);
  font-size: 16px;
  border-radius: 12px;
  padding: 12px;
  width: 200px;
  margin-bottom: -8px;
  margin-left: 8px;
`;

const SettingsMenuHeader = styled.div`
  display: flex;
  justify-content: space-between;
  font-weight: bold;
`;

const SettingsDivider = styled.div`
  background: var(--divider-color);
  height: 1px;
  width: 100%;
  margin: 8px 0;
`;

const SettingsLongDivider = styled.div`
  background: var(--secondary-color);
  opacity: 0.2;
  height: 1px;
  margin: 8px -12px;
`;

const SettingsTray = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const SwitchSetting = styled.div`
  align-items: center;
  display: flex;
  font-size: 12px;
  justify-content: space-between;
  margin-top: 4px;
`;

const SwitchSettingEnd = styled.div`
  align-items: center;
  display: flex;
  font-size: 12px;
  justify-content: end;
  margin-top: 4px;
`;

const UserWithPic = styled.a`
  display: flex;
  align-items: center;
  font-size: 14px;
  color: var(--primary-color);
`;

const ProfilePic = styled.img`
  border: 2px solid white;
  border-radius: 50%;
  height: 20px;
  width: 20px;
  margin-right: 8px;
`;

const FeedbackLink = styled.a`
  color: var(--secondary-color);
  display: block;
  font-size: 12px;
  text-decoration: none;
`;

const ThemeSelectors = styled.div`
  display: flex;
  align-items: center;
  margin-right: -6px;
`;

const BETALabelSpan = styled.span`
  &:after {
    content: 'BETA';
    font-size: 8px;
    margin-left: 4px;
    margin-right: 4px;
    background-color: red;
    vertical-align: top;
  }
`;

const DARK_MODE = window.matchMedia('(prefers-color-scheme: dark)').matches;

const OPEN_ARTS_BGURL_GRAPHQL_ID = 5471336762919837;
const OPEN_ARTS_BGURL_PREFIX =
  'https://interncache-all.fbcdn.net/manifold/internal_new_tab_open_arts/tree/images/';

// Default theme based on OS preference
function usePrefersDarkMode() {
  const [value, setValue] = useState(DARK_MODE);
  useEffect(() => {
    window.matchMedia('(prefers-color-scheme: dark)').addListener(e => {
      setValue(e.matches);
    }, []);
  });
  return value;
}

function _updateTheme(theme) {
  const body = document.getElementById('app');
  for (let type of ['light', 'dark', 'blue', 'revamp-light']) {
    body.classList.toggle('theme-' + type, type === theme);
  }
}

function Settings(props) {
  const user = useCurrentUser();
  const prefersDarkMode = usePrefersDarkMode();
  const [isOpen, setIsOpen] = React.useState(false);
  let disableOABackground = useGK('cpe_intern_new_tab_disable_oa_background');
  const isRevamp = useGK('intern_new_tab_revamp');
  const [theme, setTheme] = useStateWithSyncStorage(
    storageKeys.theme,
    null,
    // Default theme based on OS preference
    prefersDarkMode ? 'dark' : 'light'
  );
  const [background, setBackground] = useStateWithSyncStorage(
    storageKeys.background,
    // Default background
    disableOABackground ? 'default' : 'open_arts'
  );
  const [openArtsImage, setOpenArtsImage] = useStateWithSyncStorage(
    storageKeys.openArtsImage,
    null
  );
  const [clocksEnabled, setClocksEnabled] = useStateWithSyncStorage(
    storageKeys.clocks,
    true
  );
  const [use24Hour, setUse24Hour] = useStateWithSyncStorage(
    storageKeys.use24Hour,
    DEFAULT_24_HOUR
  );
  const [searchEnabled, setSearchEnabled] = useStateWithSyncStorage(
    storageKeys.search,
    true
  );
  const [bookmarksEnabled, setBookmarksEnabled] = useStateWithSyncStorage(
    storageKeys.bookmarks,
    false
  );
  const [libraryEnabled, setLibraryEnabled] = useStateWithSyncStorage(
    storageKeys.library,
    true
  );
  const [pxcConfirmRequired, setPxcConfirmRequired] = useStateWithSyncStorage(
    storageKeys.pxcConfirm,
    true
  );
  const [calendarEnabled, setCalendarEnabled] = useStateWithSyncStorage(
    storageKeys.calendar,
    true
  );
  const [calendarFullDay, setCalendarFullDay] = useStateWithSyncStorage(
    storageKeys.calendarFullDay,
    false
  );
  const [goToBunnyWithEnter, setGoToBunnyWithEnter] = useStateWithSyncStorage(
    storageKeys.goToBunnyWithEnter,
    false
  );
  const [earlyMeetingEnabled, setEarlyMeetingEnabled] = useStateWithSyncStorage(
    storageKeys.earlyMeeting,
    false
  );
  const [metamateCta, setMetamateCta] = useStateWithSyncStorage(
    storageKeys.metamateCta,
    true
  );
  const [tipsAndTricksEnabled, setTipsAndTricksEnabled] =
    useStateWithSyncStorage(storageKeys.tipsAndTricks, false);

  const [diffsEnabled, setDiffsEnabled] = useDiffsEnabled();

  useEffect(() => {
    if (!isRevamp) {
      theme && _updateTheme(theme);
    }
  }, [theme, isRevamp]);

  useEffect(() => {
    let mounted = true;
    let imgObj = null;

    if (background === 'open_arts' && !disableOABackground && !isRevamp) {
      genGraphQL(OPEN_ARTS_BGURL_GRAPHQL_ID)
        .then(data => {
          if (!mounted) return;
          imgObj = data['intern_new_tab_background_images'];
          imgObj['image_url'] =
            OPEN_ARTS_BGURL_PREFIX + imgObj['image_file_name'];
        })
        .then(() => {
          if (!mounted) return;
          // Preloading the image into browser cache and only showing it once loaded.
          // This would prevent the background being partially displayed while downloading.
          const img = new Image();
          img.src = imgObj['image_url'];
          if (!img.complete) {
            // This image is not present in the browser cache
            return img.decode();
          }
        })
        .catch(() => {
          if (!mounted) return;
          imgObj = {
            title: 'Brought to you by @MetaOpenArts',
            image_url: 'bg/bg-openarts-default.jpg',
          };
        })
        .then(() => {
          if (!mounted) return;
          setOpenArtsImage(imgObj);
        });
    }
    return () => {
      mounted = false;
    };
  }, [background, setOpenArtsImage, disableOABackground, isRevamp]);

  useEffect(() => {
    const body = document.getElementById('app');

    if (isRevamp || background === 'none') {
      body.style.removeProperty('background-image');
    } else if (background === 'default') {
      body.style.backgroundImage = `url('bg/bg-${theme}.jpg')`;
    } else if (background === 'open_arts' && disableOABackground) {
      setBackground('default');
      body.style.backgroundImage = `url('bg/bg-${theme}.jpg')`;
    } else if (background === 'open_arts') {
      if (openArtsImage) {
        body.style.backgroundImage = `radial-gradient(circle, rgba(32, 35, 39, 0.5), #0004), url(${openArtsImage['image_url']})`;
      }
    }
  }, [
    theme,
    background,
    openArtsImage,
    disableOABackground,
    setBackground,
    isRevamp,
  ]);

  if (!theme) {
    return null;
  }

  const getSelectedBackgroundText = () => {
    if (background === null) {
      return '';
    }
    return background
      .split('_')
      .map(word => word[0].toUpperCase() + word.slice(1).toLowerCase())
      .join(' ');
  };

  return (
    <Tippy content="Settings">
      <Tippy
        arrow={false}
        placement="top-end"
        interactive={true}
        visible={isOpen}
        onClickOutside={() => setIsOpen(false)}
        className="tippyPopover"
        content={
          <SettingsMenu>
            <SettingsMenuHeader>
              <div>Settings</div>
              <CloseButton onClick={() => setIsOpen(false)} />
            </SettingsMenuHeader>
            <SettingsLongDivider />
            <SwitchSetting>
              <span>Theme: {theme[0].toUpperCase() + theme.slice(1)}</span>
              <ThemeSelectors>
                <label
                  className={classNames({
                    settingsRadioContainer: true,
                    settingsRadioContainerChecked: theme === 'blue',
                  })}
                  onClick={() => setTheme('blue')}
                >
                  <span className="settingsRadioCheckmark settingsRadioCheckmarkBlue" />
                </label>
                <label
                  className={classNames({
                    settingsRadioContainer: true,
                    settingsRadioContainerChecked: theme === 'light',
                  })}
                  onClick={() => setTheme('light')}
                >
                  <span className="settingsRadioCheckmark settingsRadioCheckmarkLight" />
                </label>
                <label
                  className={classNames({
                    settingsRadioContainer: true,
                    settingsRadioContainerChecked: theme === 'dark',
                  })}
                  onClick={() => setTheme('dark')}
                >
                  <span className="settingsRadioCheckmark settingsRadioCheckmarkDark" />
                </label>
              </ThemeSelectors>
            </SwitchSetting>
            <SettingsDivider />
            {!disableOABackground && (
              <>
                <SwitchSetting>
                  <span>
                    <BETALabelSpan>Background</BETALabelSpan>:{' '}
                    {getSelectedBackgroundText()}
                  </span>
                </SwitchSetting>
                <SwitchSettingEnd>
                  <ThemeSelectors>
                    <Tippy content="No background">
                      <label
                        className={classNames({
                          settingsRadioContainer: true,
                          settingsRadioContainerChecked: background === 'none',
                        })}
                        onClick={() => setBackground('none')}
                      >
                        <span className="settingsRadioCheckmark settingsRadioCheckmarkDark" />
                      </label>
                    </Tippy>
                    <Tippy content="Default background">
                      <label
                        className={classNames({
                          settingsRadioContainer: true,
                          settingsRadioContainerChecked:
                            background === 'default',
                        })}
                        onClick={() => setBackground('default')}
                      >
                        <span className="settingsRadioCheckmark settingsRadioCheckmarkDark" />
                      </label>
                    </Tippy>
                    <Tippy content="Curated images by Open Arts Team">
                      <label
                        className={classNames({
                          settingsRadioContainer: true,
                          settingsRadioContainerChecked:
                            background === 'open_arts',
                        })}
                        onClick={() => setBackground('open_arts')}
                      >
                        <span className="settingsRadioCheckmark settingsRadioCheckmarkDark" />
                      </label>
                    </Tippy>
                  </ThemeSelectors>
                </SwitchSettingEnd>
              </>
            )}
            <SettingsDivider />
            <SwitchSetting>
              24-hour Time
              <Switch toggled={use24Hour} onToggle={setUse24Hour} />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              World Clocks
              <Switch toggled={clocksEnabled} onToggle={setClocksEnabled} />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Early Meeting Warning
              <Switch
                toggled={earlyMeetingEnabled}
                onToggle={setEarlyMeetingEnabled}
              />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Search
              <Switch toggled={searchEnabled} onToggle={setSearchEnabled} />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Calendar
              <Switch toggled={calendarEnabled} onToggle={setCalendarEnabled} />
            </SwitchSetting>
            <SwitchSetting>
              Show Full Day
              <Switch toggled={calendarFullDay} onToggle={setCalendarFullDay} />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Bookmarks
              <Switch
                toggled={bookmarksEnabled}
                onToggle={setBookmarksEnabled}
              />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Library
              <Switch toggled={libraryEnabled} onToggle={setLibraryEnabled} />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Confirm Pixelcloud upload
              <Switch
                toggled={pxcConfirmRequired}
                onToggle={setPxcConfirmRequired}
              />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Tips and Tricks
              <Switch
                toggled={tipsAndTricksEnabled}
                onToggle={setTipsAndTricksEnabled}
              />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Go to Bunny with enter
              <Switch
                toggled={goToBunnyWithEnter}
                onToggle={setGoToBunnyWithEnter}
              />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Metamate
              <Switch toggled={metamateCta} onToggle={setMetamateCta} />
            </SwitchSetting>
            <SettingsDivider />
            <SwitchSetting>
              Diffs
              <Switch
                toggled={diffsEnabled}
                onToggle={value => {
                  setDiffsEnabled(value);
                  genLog('settings_diffs_toggle', value.toString());
                }}
              />
            </SwitchSetting>
            <SettingsLongDivider />
            <SettingsTray>
              {user && (
                <UserWithPic href={getInternURL('/profile/?id=' + user.fbid)}>
                  <ProfilePic src={user.profilePicture} />
                  {user.firstName}
                </UserWithPic>
              )}
              <FeedbackLink href={'https://fburl.com/internnewtab'}>
                Feedback
              </FeedbackLink>
            </SettingsTray>
          </SettingsMenu>
        }
      >
        <SettingsWrapper
          hasAttribution={background === 'open_arts'}
          className="settingsWrapper"
        >
          <SettingsButton onClick={() => setIsOpen(o => !o)}>
            <SettingsButtonImage src="/settings.png" />
          </SettingsButton>
        </SettingsWrapper>
      </Tippy>
    </Tippy>
  );
}

export default Settings;
