import {useCallback, useEffect, useState} from 'react';
import genLog from './genLog.js';
import {gen, genSet} from './SyncStorage.js';

export default function useStateWithSyncStorage(
  key,
  defaultValue,
  remoteDefaultValue
) {
  const [value, setValue] = useState(defaultValue);

  useEffect(() => {
    gen(key).then(localData =>
      setValue(
        localData === null || localData === undefined
          ? remoteDefaultValue || defaultValue
          : JSON.parse(localData)
      )
    );
    chrome.storage.onChanged.addListener(function (changes, namespace) {
      for (let k in changes) {
        let storageChange = changes[key];
        if (
          k === key &&
          storageChange.newValue !== value &&
          namespace === 'sync'
        ) {
          setValue(JSON.parse(storageChange.newValue));
        }
      }
    });
  }, [key]);

  const setValueWrapper = useCallback(
    newValue => {
      const json = JSON.stringify(newValue);
      genSet(key, json);
      genLog('change_' + key, json);
      setValue(newValue);
    },
    [key]
  );

  return [value, setValueWrapper];
}
