import React from 'react';
import styled from 'styled-components';
import Tippy from '@tippyjs/react';

const BugbubButton = styled.button`
  border-radius: 50%;
  border: 0;
  cursor: pointer;
  padding: 8px 8px 6px;
  outline: 0;
  opacity: 0.9;
  &:hover {
    opacity: 1;
  }
  background: var(--overlay-background);
`;

const BugnubButtonImage = styled.img`
  width: 18px;
  height: 18px;
  filter: var(--black-icon-filter);
`;

const BugnubWrapper = styled.div`
  position: absolute;
  color: var(--primary-color);
  &:hover {
    opacity: 1;
    background-color: var(--overlay-background);
  }
  background-color: var(--overlay-background-opaque);
  border-radius: 10%;
  margin: 4px 8px;
  left: 0;
  bottom: 110px;
`;

const REPORT_BUG =
  'https://www.internalfb.com/tasks?p_nav=MY_TASKS&template=1920061201520276';

function Bugnub(props) {
  return (
    <Tippy content="Bugnub" placement="right">
      <BugnubWrapper>
        <BugbubButton
          onClick={() => {
            chrome.tabs.create({url: REPORT_BUG});
          }}
        >
          <BugnubButtonImage src="/bug_icon.png" />
        </BugbubButton>
      </BugnubWrapper>
    </Tippy>
  );
}

export default Bugnub;
