import {useEffect, useState} from 'react';
import styled from 'styled-components';
import classNames from 'classnames';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys, DEFAULT_24_HOUR} from './LocalStorage.js';
import useMetamate from './useMetamate.js';
import useCurrentUser from './useCurrentUser.js';
import CloseButton from './CloseButton.js';
import getInternURL from './getInternURL.js';
import Switch from './Switch.js';
import Tippy from '@tippyjs/react';
import genLog from './genLog.js';
import useDiffsEnabled from './diff/useDiffsEnabled.js';
import ThemedTooltip from './ThemedTooltip.js';

const SettingsButton = styled.button`
  background: transparent;
  border: 0;
  cursor: pointer;
  padding: 0;
  outline: 0;
`;

const SettingsButtonImage = styled.img`
  width: 16px;
  height: 16px;
  filter: var(--black-icon-filter);
`;

const SettingsMenu = styled.div`
  background: var(--overlay-background);
  color: var(--primary-color);
  font-size: 16px;
  border-radius: 12px;
  padding: 12px;
  width: 200px;
  margin-bottom: -8px;
  margin-left: 8px;
`;

const SettingsMenuHeader = styled.div`
  display: flex;
  justify-content: space-between;
  font-weight: bold;
`;

const SettingsDivider = styled.div`
  background: var(--divider-color);
  height: 1px;
  width: 100%;
  margin: 8px 0;
`;

const SettingsLongDivider = styled.div`
  background: var(--secondary-color);
  opacity: 0.2;
  height: 1px;
  margin: 8px -12px;
`;

const SettingsTray = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const SwitchSetting = styled.div`
  align-items: center;
  display: flex;
  font-size: 12px;
  justify-content: space-between;
  margin-top: 4px;
`;

const UserWithPic = styled.a`
  display: flex;
  align-items: center;
  font-size: 14px;
  color: var(--primary-color);
`;

const ProfilePic = styled.img`
  border: 2px solid white;
  border-radius: 50%;
  height: 20px;
  width: 20px;
  margin-right: 8px;
`;

const FeedbackLink = styled.a`
  color: var(--secondary-color);
  display: block;
  font-size: 12px;
  text-decoration: none;
`;

const ThemeSelectors = styled.div`
  display: flex;
  align-items: center;
  margin-right: -6px;
`;

const NavBarBottomItem = styled.a`
  font-family: SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif;
  align-items: center;
  border-radius: 6px;
  color: var(--primary-color);
  cursor: pointer;
  display: flex;
  font-size: 14px;
  gap: 8px;
  height: 20px;
  margin: 0;
  opacity: 1;
  align-self: flex-start;
  padding: 8px 10px;
  &:hover {
    background-color: var(--hover-overlay-color);
    text-decoration: none;
  }
  &:active {
    background-color: var(--focus-overlay-color);
    text-decoration: none;
  }
`;

const DARK_MODE = window.matchMedia('(prefers-color-scheme: dark)').matches;

// Default theme based on OS preference
function usePrefersDarkMode() {
  const [value, setValue] = useState(DARK_MODE);
  useEffect(() => {
    window.matchMedia('(prefers-color-scheme: dark)').addListener(e => {
      setValue(e.matches);
    }, []);
  });
  return value;
}

function _updateTheme(theme) {
  const body = document.getElementById('app');
  body.style.removeProperty('background-image');
  for (let type of ['revamp-light', 'revamp-dark']) {
    body.classList.toggle('theme-' + type, type === theme);
  }
}

function Settings(props) {
  const user = useCurrentUser();
  const prefersDarkMode = usePrefersDarkMode();
  const {hasMetamate} = useMetamate();
  const [isOpen, setIsOpen] = useState(false);
  const [theme, setTheme] = useStateWithSyncStorage(
    storageKeys.theme,
    null,
    // Default theme based on OS preference
    prefersDarkMode ? 'dark' : 'light'
  );
  const [clocksEnabled, setClocksEnabled] = useStateWithSyncStorage(
    storageKeys.clocks,
    true
  );
  const [use24Hour, setUse24Hour] = useStateWithSyncStorage(
    storageKeys.use24Hour,
    DEFAULT_24_HOUR
  );
  const [searchEnabled, setSearchEnabled] = useStateWithSyncStorage(
    storageKeys.search,
    true
  );
  const [libraryEnabled, setLibraryEnabled] = useStateWithSyncStorage(
    storageKeys.library,
    true
  );
  const [calendarEnabled, setCalendarEnabled] = useStateWithSyncStorage(
    storageKeys.calendar,
    true
  );
  const [calendarFullDay, setCalendarFullDay] = useStateWithSyncStorage(
    storageKeys.calendarFullDay,
    false
  );
  const [metamateCta, setMetamateCta] = useStateWithSyncStorage(
    storageKeys.metamateCta,
    true
  );
  const [goToBunnyWithEnter, setGoToBunnyWithEnter] = useStateWithSyncStorage(
    storageKeys.goToBunnyWithEnter,
    false
  );
  const [bookmarksEnabled, setBookmarksEnabled] = useStateWithSyncStorage(
    storageKeys.bookmarks,
    false
  );

  const [diffsEnabled, setDiffsEnabled] = useDiffsEnabled();

  useEffect(() => {
    if (theme === 'light') {
      _updateTheme('revamp-light');
    } else if (theme === 'dark' || theme === 'blue') {
      _updateTheme('revamp-dark');
    }
  }, [theme]);

  useEffect(() => {
    const body = document.getElementById('app');
    body.classList.toggle('bookmarksEnabled', bookmarksEnabled);
  });

  if (!theme) {
    return null;
  }

  const tippyContent = (
    <SettingsMenu>
      <SettingsMenuHeader>
        <div>Settings</div>
        <CloseButton onClick={() => setIsOpen(false)} />
      </SettingsMenuHeader>
      <SettingsLongDivider />
      <SwitchSetting>
        <span>Theme: {theme === 'light' ? 'Light' : 'Dark'}</span>
        <ThemeSelectors>
          <label
            className={classNames({
              settingsRadioContainer: true,
              settingsRadioContainerChecked: theme === 'light',
            })}
            onClick={() => setTheme('light')}>
            <span className="settingsRadioCheckmark settingsRadioCheckmarkLight" />
          </label>
          <label
            className={classNames({
              settingsRadioContainer: true,
              settingsRadioContainerChecked:
                theme === 'dark' || theme === 'blue',
            })}
            onClick={() => setTheme('dark')}>
            <span className="settingsRadioCheckmark settingsRadioCheckmarkDark" />
          </label>
        </ThemeSelectors>
      </SwitchSetting>
      <SettingsDivider />
      <SwitchSetting>
        24-hour Time
        <Switch toggled={use24Hour} onToggle={setUse24Hour} />
      </SwitchSetting>
      <SettingsDivider />
      <SwitchSetting>
        World Clocks
        <Switch toggled={clocksEnabled} onToggle={setClocksEnabled} />
      </SwitchSetting>
      <SettingsDivider />
      <SwitchSetting>
        Search
        <Switch toggled={searchEnabled} onToggle={setSearchEnabled} />
      </SwitchSetting>
      <SettingsDivider />
      <SwitchSetting>
        Calendar
        <Switch toggled={calendarEnabled} onToggle={setCalendarEnabled} />
      </SwitchSetting>
      <SettingsDivider />
      <SwitchSetting>
        Show Full Day
        <Switch toggled={calendarFullDay} onToggle={setCalendarFullDay} />
      </SwitchSetting>
      <SettingsDivider />
      <SwitchSetting>
        Library
        <Switch toggled={libraryEnabled} onToggle={setLibraryEnabled} />
      </SwitchSetting>
      <SettingsDivider />
      {hasMetamate && (
        <>
          <SwitchSetting>
            Metamate
            <Switch toggled={metamateCta} onToggle={setMetamateCta} />
          </SwitchSetting>
          <SettingsDivider />
        </>
      )}
      <SwitchSetting>
        Diffs
        <Switch
          toggled={diffsEnabled}
          onToggle={value => {
            setDiffsEnabled(value);
            genLog('settings_diffs_toggle', value.toString());
          }}
        />
      </SwitchSetting>
      <SettingsDivider />
      <SwitchSetting>
        Go to Bunny with enter
        <Switch toggled={goToBunnyWithEnter} onToggle={setGoToBunnyWithEnter} />
      </SwitchSetting>
      <SettingsDivider />
      <SwitchSetting>
        Bookmarks
        <Switch toggled={bookmarksEnabled} onToggle={setBookmarksEnabled} />
      </SwitchSetting>
      <SettingsLongDivider />
      <SettingsTray>
        {user && (
          <UserWithPic href={getInternURL('/profile/?id=' + user.fbid)}>
            <ProfilePic src={user.profilePicture} />
            {user.firstName}
          </UserWithPic>
        )}
        <FeedbackLink href={'https://fburl.com/internnewtab'}>
          Feedback
        </FeedbackLink>
      </SettingsTray>
    </SettingsMenu>
  );

  const settingsImage = <SettingsButtonImage src="/settings_outline.png" />;

  // TODO: this is hacky and should be refactored
  // the reason to do this way is because we are using 2 different tooltip
  // 1. tippyjs
  // 2. ThemedTooltip (MUI tooltip)
  // The issue is when user click the icon, the tippyjs still show the tooltip `Settings` which blocks the settings menu
  const baseComponent = (
    <NavBarBottomItem>
      <Tippy
        arrow={false}
        placement="right-end"
        interactive={true}
        visible={isOpen}
        onClickOutside={() => setIsOpen(false)}
        className="tippyPopover"
        content={tippyContent}>
        <SettingsButton onClick={() => setIsOpen(o => !o)}>
          {settingsImage}
        </SettingsButton>
      </Tippy>
    </NavBarBottomItem>
  );

  if (isOpen) {
    return baseComponent;
  } else {
    return (
      <ThemedTooltip title="Settings" placement="right">
        <NavBarBottomItem>
          <Tippy
            arrow={false}
            placement="right-end"
            interactive={true}
            visible={isOpen}
            onClickOutside={() => setIsOpen(false)}
            className="tippyPopover"
            content={tippyContent}>
            <SettingsButton onClick={() => setIsOpen(o => !o)}>
              {settingsImage}
            </SettingsButton>
          </Tippy>
        </NavBarBottomItem>
      </ThemedTooltip>
    );
  }
}

export default Settings;
