import {
  connectedSearchLog,
  InternSearchInteractionEvent,
} from './ConnectedSearchLogger';

const TYPEAHEAD_SOURCES = {
  diff: 'diff',
  task: 'task',
  tool: 'tool',
  person: 'person',
  suggestion: 'suggestion',
};

