import React from 'react';
import Timestamp from './Timestamp.js';
import getDayLabel from './getDayLabel.js';
import styled from 'styled-components';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys} from './LocalStorage.js';

const TimeWrapper = styled.div`
  display: inline-flex;
  flex-direction: row;
  margin-top: 8px;
  cursor: pointer;
  padding: 8px 16px;
  border-radius: 10px;

  &:hover {
    background-color: var(--overlay-background);
  }
  white-space: nowrap;
  justify-self: flex-end;
  .searchFocused & {
    display: none;
  }
`;

export default function Time(props) {
  const [timeDisplaySize, setTimeDisplaySize] = useStateWithSyncStorage(
    storageKeys.timeDisplaySize,
    'Medium' // '' signifies Small, Medium or Large.
  );
  const [background] = useStateWithSyncStorage(
    storageKeys.background,
    // Default background
    'open_arts'
  );

  function changeTimeSize() {
    if (timeDisplaySize === '') {
      setTimeDisplaySize('Medium');
    } else if (timeDisplaySize === 'Medium') {
      setTimeDisplaySize('Large');
    } else setTimeDisplaySize('');
  }

  const messageClassName = `message${timeDisplaySize} secondaryColor`;
  if (props.useDayLabel != null && props.useDayLabel === true) {
    return (
      <div className="message">
        <div
          className={
            'time ' +
            (background === 'open_arts' ? 'quaternaryColor' : 'primaryColor')
          }
        >
          {getDayLabel(props.now)}
        </div>
      </div>
    );
  }

  return (
    <div className="message">
      <div className={messageClassName}>
        <div
          className={
            'time ' +
            (background === 'open_arts' ? 'quaternaryColor' : 'primaryColor')
          }
        >
          <TimeWrapper onClick={changeTimeSize}>
            <Timestamp time={props.now} split={true} />
          </TimeWrapper>
        </div>
      </div>
    </div>
  );
}
