import React from 'react';
import styled from 'styled-components';

const Button = styled.button`
  border: 0;
  background: none;
  cursor: pointer;

  opacity: 0.8;
  :hover {
    opacity: 1;
  }
`;

const Image = styled.img`
  width: 12px;
  height: 12px;
  filter: var(--icon-filter);
`;

function CloseButton(props) {
  return (
    <Button {...props}>
      <Image src={'/cross.png'} />
    </Button>
  );
}

export default CloseButton;
