import React from 'react';
import styled from 'styled-components';
import IconButton from '@mui/material/IconButton';
import ThemedTooltip from './ThemedTooltip.js';

export const ButtonIcon = styled.img`
  height: 16px;
  width: 16px;
`;

export default function ThemedIconButton(props) {
  return (
    <ThemedTooltip
      title={props.tooltip}
      onOpen={props.onTooltipOpen}
      onClose={props.onTooltipClose}
    >
      <IconButton
        onClick={props.onClick}
        style={{
          visibility: props.visibility,
        }}
        sx={{
          p: props.padding ?? '4px 8px',
          borderRadius: '8px',
          '&:hover': {
            backgroundColor: 'var(--hover-overlay-color)',
          },
          '&:active': {
            backgroundColor: 'var(--focus-overlay-color)',
          },
        }}
      >
        <ButtonIcon
          src={props.src}
          style={{
            filter: props.iconFilter ?? 'var(--secondary-icon-filter)',
          }}
        />
      </IconButton>
    </ThemedTooltip>
  );
}
