import {formatDistanceToNowStrict} from 'date-fns';

export default function formatRelativeTime(timestamp) {
  return formatDistanceToNowStrict(timestamp, {
    addSuffix: true,
    locale: {
      formatDistance: (token, count, options) => {
        let result = '';
        if (token === 'xSeconds') {
          result = `${count}s`;
        } else if (token === 'xMinutes') {
          result = `${count}m`;
        } else if (token === 'xHours') {
          result = `${count}h`;
        } else if (token === 'xDays') {
          result = `${count}d`;
        } else if (token === 'xMonths') {
          result = `${count}m`;
        } else if (token === 'xYears') {
          result = `${count}y`;
        } else {
          return '';
        }

        if (options.addSuffix) {
          if (options.comparison > 0) {
            return 'in ' + result
          } else {
            return result + ' ago'
          }
        }
        return result;
      },
    },
  });
}
