import React, {useState, useCallback} from 'react';
import {getLeft, getRight} from './calendarPosition.js';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys} from './LocalStorage.js';
import genLog from './genLog.js';
import EventPopover from './EventPopover.js';
import EventPopoverV2 from './EventPopoverV2.js';
import Tippy from '@tippyjs/react';
import useGK from './useGK.js';

function underscoreToCamelcase(string) {
  return string.replace(/(_\w)/g, function (m) {
    return m[1].toUpperCase();
  });
}

export function getColorClass(event) {
  let cssClass = 'event';
  if (event.color != null && event.color !== '') {
    cssClass +=
      ' event' + underscoreToCamelcase('_' + event.color.toLowerCase());
  }
  if (event.availability === 'TENTATIVE') {
    cssClass += ' eventTentative';
  } else if (event.availability === 'FREE') {
    cssClass += ' eventFree';
  }

  return cssClass;
}

function Event(props) {
  const isRevamp = useGK('intern_new_tab_revamp');
  const [calendarFullDay] = useStateWithSyncStorage(
    storageKeys.calendarFullDay,
    false
  );
  const start = new Date(props.event.start_time * 1000);
  const end = new Date(props.event.end_time * 1000);
  const left = getLeft(start, calendarFullDay);
  const right = getRight(end, calendarFullDay);
  const top = props.top + 30 + 'px';
  const height = props.height + 'px';

  const [timer, setTimer] = useState(0);
  const setLogTimer = useCallback(() => {
    const newTimer = setTimeout(
      () => genLog('mouse_over_event', props.event.start_time),
      1000
    );
    setTimer(newTimer);
  }, [props.event.start_time]);
  const clearLogTimer = useCallback(() => {
    clearTimeout(timer);
  }, [timer]);

  if (left == null || right == null) {
    return null;
  }

  if (isRevamp) {
    return (
      <Tippy
        singleton={props.tippySingleton}
        className="tippyPopover"
        content={<EventPopoverV2 event={props.event} />}
        offset={[0, 0]}
        interactive={true}
        onHide={clearLogTimer}
        onShow={setLogTimer}
        appendTo={document.body}
        placement="bottom"
        arrow={false}>
        <div
          aria-label={props.event.title}
          className={getColorClass(props.event)}
          style={{left, right, top, height}}
          tabindex="0"
        />
      </Tippy>
    );
  }

  return (
    <Tippy
      className="tippyPopover"
      content={<EventPopover event={props.event} />}
      interactive={true}
      onHide={clearLogTimer}
      onShow={setLogTimer}
      appendTo={document.body}>
      <div
        aria-label={props.event.title}
        className={getColorClass(props.event)}
        style={{left, right, top, height}}
        tabindex="0"
      />
    </Tippy>
  );
}

export default Event;
