import React from 'react';
import Timestamp from './Timestamp.js';
import styled from 'styled-components';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys} from './LocalStorage.js';

function Placeholder() {
  return (
    <div className="earlyMeetingWrapper secondaryColor earlyMeetingWrapperHidden">
      <div className="earlyMeeting earlyMeetingHidden">
        <div className="earlyMeetingLabel earlyMeetingLabelHidden"></div>
      </div>
    </div>
  );
}

const AlarmClock = styled.img`
  width: 12px;
  height: 12px;
  vertical-align: middle;
  margin: 0px 4px 4px 4px;
  filter: var(--black-icon-filter);
`;

export default function EarlyMeeting(props) {
  const [earlyMeetingEnabled] = useStateWithSyncStorage(
    storageKeys.earlyMeeting,
    false
  );

  if (!earlyMeetingEnabled) {
    return null;
  }

  if (props.event == null) {
    return <Placeholder />;
  }

  const title = props.event.title;
  const truncate = input =>
    input.length > 20 ? `${input.substring(0, 20)}...` : input;
  const warning = `Don't forget "${truncate(title)}" tomorrow morning at `;
  const time = new Date(props.event.start_time * 1000);
  return (
    <div className="earlyMeetingWrapper secondaryColor">
      <div className="earlyMeeting">
        <div className="earlyMeetingLabel">
          <AlarmClock src="/alarm-clock-filled.png" />
          {warning}
          <Timestamp time={time} />!
          <AlarmClock src="/alarm-clock-filled.png" />
        </div>
      </div>
    </div>
  );
}

// Find an early meeting in a list
EarlyMeeting.find = function (now, list) {
  // This is what we consider early
  const hour = 10;
  const minute = 0;

  // Start at the upcoming midnight
  const startDate = new Date(now.getTime() + 24 * 60 * 60 * 1000);
  startDate.setHours(0, 0, 0, 0);
  const startDateTS = startDate.getTime() / 1000;

  // End at the upcoming threshold
  const endDate = new Date(startDate);
  endDate.setHours(hour, minute);
  const endDateTS = endDate.getTime() / 1000;

  return list.find(
    e => e.start_time >= startDateTS && e.start_time < endDateTS
  );
};
