import {genCachedGraphQL} from './GraphQL.js';

export default function genJoinInVRLink(link_url, should_include, source, cb) {
  return genCachedGraphQL(
    '7922891711172968',
    {link_url, should_include, source},
    86400,
    data => cb(data?.viewer?.workrooms_info?.joinable_vr_link)
  );
}
