import React from 'react';

const re = /(<strong>|<\/strong>)/g;

export default function SearchResultText(props) {
  if (!props.text) {
    return null;
  }
  const parts = props.text.split(re);
  const textParts = [];
  let start = 0;
  for (let i = 0; i < parts.length; i++) {
    if (parts[i] === '<strong>') {
      start = i;
      while (parts[i] !== '</strong>') {
        i++;
      }
      textParts.push(
        <span className={'searchResultHighlight'} key={i}>
          {parts.slice(start + 1, i).join('')}
        </span>
      );
    } else {
      textParts.push(parts[i]);
    }
  }
  return <span>{textParts}</span>;
}
