import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  position: relative;
  width: 50px;
`;

const Image = styled.img`
  width: 50px;
  height: 50px;
  border-radius: 50%;
`;

const IconImageWrapper = styled.div`
  position: absolute;
  right: -5px;
  bottom: -3px;
  border-radius: 50%;
  overflow: hidden;
  padding: 2px;
  height: 24px;
  width: 24px;
  box-sizing: border-box;
  background-color: var(--overlay-background);
`;

const IconImage = styled.img`
  height: 20px;
  width: 20px;
  border-radius: 50%;
`;

function ImageWithIcon({srcs, icon}) {
  return (
    <Wrapper>
      <Image src={srcs[0]} />
      <IconImageWrapper>
        <IconImage src={icon} />
      </IconImageWrapper>
    </Wrapper>
  );
}

export default ImageWithIcon;
