import {useEffect, useState} from 'react';

export function genTree(cb) {
  if (!chrome.bookmarks) {
    return;
  }
  return new Promise(resolve =>
    chrome.bookmarks.getTree(tree => {
      const bar = tree[0].children.filter(c => c.title === 'Bookmarks Bar')[0];
      if (!bar) {
        return cb([]);
      }
      // get only links for now
      const barlinks = bar.children.filter(c => c.title.length > 0);
      cb(barlinks);
    })
  );
}

export function useBookmarkList() {
  const [bookmarks, setBookmarks] = useState([]);
  useEffect(() => {
    genTree(bm => setBookmarks(bm));
  }, []);
  return bookmarks;
}
