import React from 'react';
import styled, {css} from 'styled-components';
import {genGraphQL, genCachedGraphQL, genClearGraphQL} from './GraphQL.js';
import genLog from './genLog.js';
import CloseButton from './CloseButton.js';
import useTime from './useTime.js';
import RightPaneButton from './RightPaneButton.js';
import useGK from './useGK.js';

const Wrapper = styled.div`
  margin-top: 16px;
`;

const Menu = styled.div`
  background-color: var(--overlay-background);
  color: var(--primary-color);
  border-radius: 10px;
  padding: 10px 12px;
  max-height: calc(100vh - 150px);
  overflow: auto;
`;

const MenuV2 = styled.div`
  background-color: var(--overlay-background);
  color: var(--primary-color);
  border-radius: 10px;
  padding: 10px 12px;
  max-height: calc(100vh - 150px);
  overflow: auto;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  z-index: 2;
`;

const Divider = styled.div`
  height: 1px;
  width: 100%;
  min-width: 200px;
  background-color: var(--divider-color);
  margin: 8px 0;
`;

const MenuHeader = styled.div`
  display: flex;
  justify-content: space-between;
  font-size: 16px;
`;

const Input = styled.input`
  border: 0;
  font-size: 16px;
  background: var(--overlay-background);
  color: var(--primary-color);
  padding: 4px;
  outline: 0;
  ::placeholder {
    color: var(--secondary-color);
    font-size: 12px;
  }
`;

const InputV2 = styled.input`
  border: 0;
  font-size: 14px;
  background: var(--overlay-background);
  color: var(--primary-color);
  padding: 4px;
  outline: 0;
  ::placeholder {
    color: var(--secondary-color);
    font-size: 12px;
  }
`;

const NewTodoWrapper = styled.div`
  display: flex;
  align-items: center;
  margin-top: -4px;
`;

const TodoWrapper = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 8px;
`;

const ClosedIcon = styled.i`
  width: 20px;
  margin-right: 4px;
  height: 20px;
  background-image: url(/checkmark-done.png);
  background-size: 20px 20px;
  flex-shrink: 0;

  ${TodoWrapper}:hover & {
    background-image: url(/circle.png);
    filter: var(--icon-filter);
  }
`;

const NewIcon = styled.img`
  width: 20px;
  height: 20px;
  margin-right: 4px;
  filter: var(--icon-filter);
`;

const OpenIcon = styled.i`
  cursor: pointer;
  margin-right: 4px;
  width: 20px;
  height: 20px;
  background-size: 20px 20px;
  background-image: url(/circle.png);
  filter: var(--icon-filter);
  flex-shrink: 0;

  ${TodoWrapper}:hover & {
    background-image: url(/checkmark-done.png);
    filter: unset;
  }
`;

const TodoLabel = styled.span`
  padding-bottom: 1px;
  font-size: 14px;
  font-weight: 400;
  font-family: SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif;
  max-width: 200px;
  overflow: hidden;
  margin-right: 4px;
  width: 100%;
  margin-left: 3px;
  white-space: pre-wrap;
  ${props =>
    props.isClosed &&
    css`
      opacity: 0.6;
      text-decoration: line-through;
    `};
`;

const TodoCloseWrapper = styled.div`
  opacity: 0;
  ${TodoWrapper}:hover & {
    opacity: 1;
  }
`;

function _createHomeTodo(text, cb) {
  genGraphQL('2819119511474744', {text}).then(data => {
    cb(data.intern_todo_create_text_item.item.id);
  });
}

function _toggleHomeTodo(fbid, is_closed) {
  genGraphQL('2761075797347060', {fbid, is_closed});
}

function _deleteHomeTodo(fbid) {
  genGraphQL('2766257746825881', {fbid});
}

const TODO_FBID = '2936248709755889';

function clearCache() {
  genClearGraphQL(TODO_FBID, null);
}

function Todo({isOpen, onOpen, onClose}) {
  const now = useTime(60000);
  const [newItem, setNewItem] = React.useState('');
  const inputRef = React.useRef();
  const [todos, setTodos] = React.useState([]);
  const isRevamp = useGK('intern_new_tab_revamp');

  React.useEffect(() => {
    genCachedGraphQL(
      TODO_FBID,
      null,
      3600,
      data => {
        const nodes = data.viewer.actor.intern_user.intern_todo_items.edges
          .map(edge => edge.node)
          .filter(
            node =>
              !node.is_snoozed &&
              (node.snooze_until_time == null || node.snooze_until_time <= now)
          );
        setTodos(
          nodes.map(node => ({
            fbid: node.id,
            isClosed: false,
            value: (node.attached_items || [])
              .map(i => i.intern_todo_item_title)
              .concat([node.text?.rte_content_plain_text || ''])
              .filter(Boolean)
              .join('\n'),
          }))
        );
      },
      true
    );
  }, [now]);

  return (
    <Wrapper>
      {isOpen ? (
        isRevamp ? (
          <MenuV2>
            <MenuHeader>
              To Do
              <CloseButton onClick={onClose} />
            </MenuHeader>
            <Divider />
            {todos.map((todo, idx) => (
              <TodoWrapper
                onClick={() => {
                  const newTodos = [...todos];
                  newTodos[idx].isClosed = !todo.isClosed;
                  setTodos(newTodos);
                  _toggleHomeTodo(newTodos[idx].fbid, newTodos[idx].isClosed);
                  clearCache();
                  genLog('toggle_todo');
                }}
              >
                {todo.isClosed ? <ClosedIcon /> : <OpenIcon />}
                <TodoLabel isClosed={todo.isClosed}>{todo.value}</TodoLabel>
                {todo.isClosed ? (
                  <TodoCloseWrapper>
                    <CloseButton
                      onClick={e => {
                        e.stopPropagation();
                        const newTodos = [...todos];
                        _deleteHomeTodo(newTodos[idx].fbid);
                        clearCache();
                        newTodos.splice(idx, 1);
                        setTodos(newTodos);
                        genLog('remove_todo');
                      }}
                    />
                  </TodoCloseWrapper>
                ) : null}
              </TodoWrapper>
            ))}
            {todos.length > 0 ? <Divider /> : null}
            <NewTodoWrapper>
              <NewIcon src="/plus-circle.png" />
              <InputV2
                value={newItem}
                onChange={e => setNewItem(e.target.value)}
                placeholder="Enter new item"
                onKeyPress={e => {
                  if (e.key === 'Enter') {
                    const newTodos = [
                      {value: e.target.value, isClosed: false},
                      ...todos,
                    ];
                    setTodos(newTodos);
                    setNewItem('');
                    const value = e.target.value;
                    _createHomeTodo(value, fbid => {
                      setTodos(todos =>
                        todos.map(todo => {
                          if (todo.value === value) {
                            return {...todo, fbid};
                          }
                          return todo;
                        })
                      );
                      clearCache();
                    });
                    genLog('add_todo');
                  }
                }}
                ref={inputRef}
              />
            </NewTodoWrapper>
          </MenuV2>
        ) : (
          <Menu>
            <MenuHeader>
              To Do
              <CloseButton onClick={onClose} />
            </MenuHeader>
            <Divider />
            {todos.map((todo, idx) => (
              <TodoWrapper
                onClick={() => {
                  const newTodos = [...todos];
                  newTodos[idx].isClosed = !todo.isClosed;
                  setTodos(newTodos);
                  _toggleHomeTodo(newTodos[idx].fbid, newTodos[idx].isClosed);
                  clearCache();
                  genLog('toggle_todo');
                }}
              >
                {todo.isClosed ? <ClosedIcon /> : <OpenIcon />}
                <TodoLabel isClosed={todo.isClosed}>{todo.value}</TodoLabel>
                {todo.isClosed ? (
                  <TodoCloseWrapper>
                    <CloseButton
                      onClick={e => {
                        e.stopPropagation();
                        const newTodos = [...todos];
                        _deleteHomeTodo(newTodos[idx].fbid);
                        clearCache();
                        newTodos.splice(idx, 1);
                        setTodos(newTodos);
                        genLog('remove_todo');
                      }}
                    />
                  </TodoCloseWrapper>
                ) : null}
              </TodoWrapper>
            ))}
            {todos.length > 0 ? <Divider /> : null}
            <NewTodoWrapper>
              <NewIcon src="/plus-circle.png" />
              <Input
                value={newItem}
                onChange={e => setNewItem(e.target.value)}
                placeholder="Enter new item"
                onKeyPress={e => {
                  if (e.key === 'Enter') {
                    const newTodos = [
                      {value: e.target.value, isClosed: false},
                      ...todos,
                    ];
                    setTodos(newTodos);
                    setNewItem('');
                    const value = e.target.value;
                    _createHomeTodo(value, fbid => {
                      setTodos(todos =>
                        todos.map(todo => {
                          if (todo.value === value) {
                            return {...todo, fbid};
                          }
                          return todo;
                        })
                      );
                      clearCache();
                    });
                    genLog('add_todo');
                  }
                }}
                ref={inputRef}
              />
            </NewTodoWrapper>
          </Menu>
        )
      ) : (
        <RightPaneButton
          onClick={() => {
            if (isOpen) {
              onClose();
            } else {
              onOpen();
              genLog('click_right_nav_item', 'todo');
            }
            setTimeout(() => {
              if (!isOpen && inputRef.current) {
                inputRef.current.focus();
              }
            }, 50);
          }}
          icon="/checkmark.png"
          label="To Do"
        />
      )}
    </Wrapper>
  );
}

export default Todo;
