import {gen, genSet} from './LocalStorage';
import {genGraphQL} from './GraphQL';
import {NEW_TAB_LOG} from './PersistentDocumentIds';

export const InternSearchInteractionEvent = Object.freeze({
  QUERY: 'query',
  CLICK: 'click',
  TYPEAHEAD_SUGGESTION_RENDER: 'typeahead_suggestion_render',
  TYPEAHEAD_SUGGESTION: 'typeahead_suggestion',
  TYPEAHEAD_SUGGESTION_SELECT: 'typeahead_suggestion_select',
  OPEN_CONNECTED_SEARCH_MODAL: 'open_connected_search_modal',
  CLOSE_CONNECTED_SEARCH_MODAL: 'close_connected_search_modal',
  IMPRESSION: 'impression',
  SEARCH_RESULT_VPV: 'search_result_vpv',
  WIDGET_VPVD: 'widget_vpvd',
  UNIT_INTERACTION: 'unit_interaction',
  UNIT_RENDER: 'unit_render',
});

export const InternSearchUnitType = Object.freeze({
  PROFILE: 'profile',
  NUMBER: 'number',
  FBID: 'fbid',
});

type KeyValuePayload = {|
  key: string,
  value: string,
|};

type Props = {|
  event: string,
  payload?: $ReadOnlyArray<KeyValuePayload>,
|};

const END_OF_LIFE_CALLS = 'end_of_life_calls';

export function connectedSearchLog({event, ...payload}: Props) {
  const appVersion = chrome.runtime.getManifest().version;
  const baseLog = {
    app_version: appVersion,
    container: 'intern new tab',
    event: event,
    ...payload,
  };

  getStoredData().then(data => {
    const log = {...baseLog, ...data};
    genGraphQL(NEW_TAB_LOG, log);
  });
}

async function getStoredData() {
  return {
    entry_point: '',
    entry_method: 'shortcut', //match enum
    session_id: '',
  };
}

export async function updateEndOfLifeCalls(value, addCall: boolean) {
  const jsonValue = JSON.stringify(value);
  Promise.resolve(gen(END_OF_LIFE_CALLS)).then(callbacks => {
    let callbackArray = callbacks;
    if (callbackArray == null) {
      callbackArray = [];
    }
    if (addCall) {
      callbackArray.push(jsonValue);
    }
    // Otherwise remove call, if exists
    else {
      const callIndex = callbackArray.indexOf(jsonValue);
      if (callIndex === -1) {
        return;
      }
      callbackArray.splice(callIndex, 1);
    }
    genSet(END_OF_LIFE_CALLS, callbackArray);
  });
}
