import React from 'react';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Popover from '@mui/material/Popover';
import {Box} from '@mui/system';
import styled from 'styled-components';
import Avatar from '@mui/material/Avatar';
import Tooltip from '@mui/material/Tooltip';
import {openLinkCopiedToast} from './recentlyViewedToast.utils';
import {_genLog} from './libraryRecentlyViewed.utils.js';
import ThemedIconButton from '../ThemedIconButton.js';
import formatRelativeTime from '../formatRelativeTime.js';

export const CopyLinkIcon = styled.img`
  height: 15px;
  width: 15px;
  filter: var(--icon-filter);
`;

const ItemIcon = styled.img`
  width: 24px;
  height: 24px;
`;

export default function DocumentPreviewPopOver(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = event => {
    event.stopPropagation();
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? 'document-preview-popover' : undefined;

  const modifiedTime = props.modifiedTime;
  const modifiedBy = props.modifiedBy;
  const modifiedByProfileUrl = props.modifiedByProfileUrl;

  const getTitle = () => {
    return (
      <Box
        sx={{display: 'flex', justifyContent: 'space-between', width: '100%'}}
      >
        <Box sx={{display: 'flex'}}>
          <ItemIcon src={props.docIconUrl} />
          <Typography
            sx={{
              fontSize: 18,
              fontWeight: 700,
              borderRadius: 6,
              margin: '0 0 0 4px',
            }}
            noWrap
          >
            {props.title}
          </Typography>
        </Box>
        <Box>
          <Tooltip title="Copy link">
            <IconButton
              onClick={event => {
                event.stopPropagation();
                // Logging
                _genLog('click_copy_link', {
                  native_id: props.native_id ?? props.url,
                  source: props.docSource,
                  type: 'document',
                  referrer: props.referringTab,
                });
                navigator.clipboard.writeText(props.url);
                openLinkCopiedToast(props.title);
              }}
              style={{
                color: 'var(--primary-color)',
              }}
            >
              <CopyLinkIcon src="/link.png" />
            </IconButton>
          </Tooltip>
        </Box>
      </Box>
    );
  };
  const getSubtitle = () => {
    return (
      <Typography
        sx={{
          fontSize: 14,
          fontWeight: 400,
          borderRadius: 6,
        }}
        display="inline-flex"
        gutterBottom
        noWrap
      >
        {
        modifiedTime &&
          <>
            {`Updated ${formatRelativeTime(modifiedTime * 1000)} by `}
            <Avatar
              sx={{width: 20, height: 20, margin: '0 6px 0 2px'}}
              alt={modifiedBy}
              src={modifiedByProfileUrl}
            />
            {modifiedBy}
          </>
        }
      </Typography>
    );
  };

  const getIdFromUrl = input => {
    return input.match(/[-\w]{25,}/);
  };
  const getPreviewURI = url => {
    const fileID = getIdFromUrl(url);
    let domain = 'https://docs.google.com/document/d/';
    let uri = '';
    if (fileID) {
      domain = url.split(fileID[0])[0];
      uri = domain
        .concat(fileID[0])
        .concat(
          '/preview?chrome=false&single=true&widget=false&headers=false&rm=minimal&ui=2&embedded=true&output=txt&gridlines=false&portrait=true'
        );
    } else {
      uri = domain
        .concat('0')
        .concat(
          '/preview?chrome=false&single=true&widget=false&headers=false&rm=minimal&ui=2&embedded=true&output=txt&gridlines=false&portrait=true'
        );
    }
    return uri;
  };

  return (
    <>
      <ThemedIconButton
        tooltip="Preview"
        onClick={event => {
          event.stopPropagation();
          // Logging
          _genLog('click_preview_link', {
            native_id: props.native_id ?? props.url,
            source: props.docSource,
            type: 'document',
            referrer: props.referringTab,
          });
          handleClick(event);
        }}
        visibility={props.visibility}
        index={id}
        src="/eye_filled.svg"
      />
      <Popover
        id={props.index}
        open={open}
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'center',
          horizontal: 'left',
        }}
        onClose={handleClose}
      >
        <Box
          sx={{
            padding: '12px',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'flex-start',
          }}
        >
          {getTitle()}
          {getSubtitle()}
          <iframe
            src={getPreviewURI(props.url)}
            title={props.title}
            width="540px"
            height="360px"
          />
        </Box>
      </Popover>
    </>
  );
}
