import {genCachedGraphQL} from './GraphQL.js';

function getInternSearchQuery(search) {
  return JSON.stringify({
    key: 'AND',
    children: [
      {
        key: 'EQUALS_ALL_OF_INTERN_SEARCH_KEYWORDS',
        field: 'INTERN_SEARCH_KEYWORDS_SEARCH',
        value: search,
      },
    ],
  });
}

export default function genInternSearch(search, cb) {
  const query = getInternSearchQuery(search);
  return genCachedGraphQL('2576697889067805', {query}, 86400, data =>
    cb(data.intern_power_search_query.search_items.edges.map(e => e.node))
  );
}
