import {memo} from 'react';
import useGK from '../useGK.js';
import styled from 'styled-components';
import Chip from '@mui/joy/Chip';
import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import {Box} from '@mui/system';
import Typography from '@mui/material/Typography';
import Timestamp from '../Timestamp.js';
import Avatar from '@mui/material/Avatar';
import CalendarMonthOutlinedIcon from '@mui/icons-material/CalendarMonthOutlined';
import Toolbar from '@mui/material/Toolbar';
import CircleIcon from '@mui/icons-material/Circle';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import {CopyLinkIcon} from './LibraryActiveDocuments.js';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import {openLinkCopiedToast} from './recentlyViewedToast.utils';
import AttachmentOutlinedIcon from '@mui/icons-material/AttachmentOutlined';
import {_genLog, getSourceIcon} from './libraryRecentlyViewed.utils.js';
import {green} from '@mui/material/colors';
import Link from '@mui/material/Link';
import useStateWithSyncStorage from '../useStateWithSyncStorage.js';
import {storageKeys} from '../LocalStorage.js';
import formatRelativeTime from '../formatRelativeTime.js';
import ThemedTooltip from '../ThemedTooltip.js';

const ItemIcon = styled.img`
  width: 24px;
  height: 24px;
  margin: 0 8px;
`;

const INTERN_PREFIX = 'https://www.internalfb.com/';

function LibraryMeetingDocuments(props) {
  const [hoveredRowIndex, setHoveredRowIndex] = React.useState(null);
  const [theme] = useStateWithSyncStorage(storageKeys.theme);
  const isDocumentTaggingAvailable = useGK('document_tagging');
  const todayEvents = props.events;
  const upNext = props.upNext;

  if (upNext != null) {
    const checkExistingMeeting = obj => obj.id === upNext.id;
    if (!todayEvents.some(checkExistingMeeting)) {
      todayEvents.push(upNext);
    }
  }

  let todayEventDocuments = todayEvents
    ?.map(event => {
      const documentSet = new Set();
      const knowledgeLinks = event?.knowledge_links;
      const meetingDocuments =
        knowledgeLinks
          ?.map(libraryDoc => {
            const id = libraryDoc?.id;
            const documentIconUrl = getSourceIcon(
              libraryDoc.source.toUpperCase(),
              libraryDoc?.type, // mimetype
              theme
            );
            const url =
              libraryDoc.source.toUpperCase() === 'KNOWLEDGE_BASE'
                ? INTERN_PREFIX + libraryDoc?.url
                : libraryDoc?.url;

            if (documentSet.has(libraryDoc?.url)) {
              return null;
            }
            documentSet.add(libraryDoc?.url);

            return {
              id: id,
              title: libraryDoc?.title,
              time: libraryDoc?.time_interacted ?? 0,
              url,
              source: libraryDoc.source,
              documentIconUrl,
              nativeId: id,
              tag: libraryDoc?.tag,
              viewedTime: libraryDoc?.viewed_time,
              modifiedBy: libraryDoc?.modifying_user,
              modifiedTime: libraryDoc?.edited_time,
            };
          })
          .filter(viewedDoc => {
            return viewedDoc != null;
          })
          .sort((a, b) => {
            return b.modifiedTime - a.modifiedTime;
          }) ?? [];
      const knowledgeAttachments = event?.attachments;
      const meetingAttachments =
        knowledgeAttachments
          ?.map(attachment => {
            const id = attachment?.id;
            const source = 'attachment';
            const url = INTERN_PREFIX + attachment?.uri;

            if (documentSet.has(attachment?.url)) {
              return null;
            }
            documentSet.add(attachment?.url);

            return {
              id: id,
              title: attachment?.filename,
              url,
              source,
              nativeId: id,
            };
          })
          .filter(viewedDoc => {
            return viewedDoc != null;
          }) ?? [];
      return {event, documents: [...meetingDocuments, ...meetingAttachments]};
    })
    .filter(event => event?.documents?.length > 0); // Filtering out events which do not have meeting docs

  const getEventTimelineCategory = eventTime => {
    // If current time is greater than eventTime, then its past
    if (Date.now() - eventTime > 0) {
      return 'past';
    }

    const eventDate = new Date(eventTime);
    const today = new Date();

    // 👇️ OPTIONAL!
    // This line sets the time of the current date to the
    // last millisecond, so the comparison returns `true` only if
    // date is at least tomorrow
    today.setHours(23, 59, 59, 998);

    if (eventDate > today) {
      return 'up_next';
    }
    return 'upcoming';
  };

  // Example : https://www.internalfb.com/intern/fiddle/wyTtl/
  // Grouping events by their timeline. Example if the events endtime is in the past,
  // they get categorized into Past section else its upcoming
  const groupByTimelineCategory = function (xs, key) {
    return xs.reduce(function (rv, x) {
      const timeline = getEventTimelineCategory(x.event[key] * 1000);
      (rv[timeline] = rv[timeline] || []).push(x);
      return rv;
    }, {});
  };
  const categorizedEventsDocuments = groupByTimelineCategory(
    todayEventDocuments,
    'end_time'
  );
  const [groupedEventsDocuments, setGroupedMeetingDocuments] = React.useState(
    categorizedEventsDocuments ?? {}
  );

  const capitalizeFirstLetter = string => {
    return (string.charAt(0).toUpperCase() + string.slice(1)).replace('_', ' ');
  };

  const getBadgeTextColor = tag => {
    switch (tag) {
      case 'PRE_READ':
        return 'rgb(95, 158, 160)';
      case 'NOTES':
        return 'rgb(147, 112, 219)';
      case 'WORKING_DOC':
        return 'rgb(0, 128, 128)';
      default:
        return 'rgb(47, 79, 79)';
    }
  };

  const getBadgeColor = tag => {
    switch (tag) {
      case 'PRE_READ':
        return 'rgb(224, 255, 255, 0.8)';
      case 'NOTES':
        return 'rgb(221, 160, 221, 0.3)';
      case 'WORKING_DOC':
        return 'rgb(0, 128, 128, 0.2)';
      default:
        return 'rgb(220, 220, 220, 0.5)';
    }
  };

  const getBadgeText = tag => {
    switch (tag) {
      case 'PRE_READ':
        return 'Pre-read';
      case 'NOTES':
        return 'Notes';
      case 'WORKING_DOC':
        return 'Working Doc';
      default:
        return null;
    }
  };

  const getAttachmentIcon = () => {
    return (
      <AttachmentOutlinedIcon
        sx={{
          width: '24px',
          height: '24px',
          margin: '0 8px',
          filter: 'var(--black-icon-filter)',
        }}
      />
    );
  };

  const isNeverSeenHighlighted = (viewedTime, modifiedTime, queryType) =>
    queryType === 'SHARED' && viewedTime === null;

  const getNewUpdateIconOpacity = (viewedTime, modifiedTime) => {
    if (
      modifiedTime != null &&
      (viewedTime == null || viewedTime < modifiedTime)
    ) {
      return 1;
    } else {
      return 0;
    }
  };

  const getTimeMetadata = (viewedTime, modifiedTime, modifiedUser) => {
    return (
      <Box
        sx={{
          color: 'var(--secondary-color)',
          display: 'inline-flex',
          alignItems: 'center',
        }}
      >
        {modifiedUser != null && modifiedUser.name != null && (
          <>
            <ThemedTooltip title={modifiedUser?.name}>
              <Avatar
                sx={{width: 24, height: 24, mr: '8px'}}
                alt={modifiedUser?.name}
                src={modifiedUser?.profile_pic_uri}
              />
            </ThemedTooltip>
            <Typography
              variant="caption"
              sx={{color: 'var(--secondary-color)'}}
              noWrap={true}
            >
              {' Updated '}
              {modifiedTime && formatRelativeTime(modifiedTime * 1000)}
            </Typography>
            <CircleIcon
              id="document-read-status-indicator"
              color="success"
              sx={{
                m: '0 6px 0 4px',
                fontSize: 12,
                color: green[600],
                opacity: getNewUpdateIconOpacity(viewedTime, modifiedTime),
              }}
            />
          </>
        )}
      </Box>
    );
  };

  const getCopyItem = (row, i) => {
    return (
      <Tooltip title="Copy link">
        <IconButton
          onClick={event => {
            event.stopPropagation();
            _genLog('click_copy_link', {
              native_id: row.native_id ?? row.url,
              source: row.source,
              type: 'document',
              referrer: 'TODAYS_MEETING_TAB',
              extra_data: {
                row: row,
              },
            });
            navigator.clipboard.writeText(row.url);
            openLinkCopiedToast(row.title);
          }}
          style={{
            visibility: hoveredRowIndex === i ? 'visible' : 'hidden',
            color: 'var(--primary-color)',
          }}
        >
          <CopyLinkIcon src="/link.png" />
        </IconButton>
      </Tooltip>
    );
  };

  const getTimelineSections = () => {
    const getRowIndex = (parentIdx, currentIdx) => {
      return parentIdx + '-' + currentIdx;
    };
    const timelineSections = [];
    for (const [timeline, eventsDocuments] of Object.entries(
      groupedEventsDocuments
    )) {
      timelineSections[timeline] = (
        <Box sx={{my: '8px'}}>
          <Box sx={{m: '8px 8px 0 8px', color: 'var(--primary-color)'}}>
            <Typography variant="body2" gutterBottom sx={{fontWeight: 600}}>
              {capitalizeFirstLetter(timeline)}
            </Typography>
          </Box>
          {eventsDocuments.map(
            (eventDocuments, parentIdx) =>
              eventDocuments != null &&
              eventDocuments?.documents?.length > 0 && (
                <Box
                  sx={{
                    m: '6px 8px',
                    pb: '6px',
                    borderRadius: 2,
                    background: 'var(--card-color)',
                    boxShadow: 3,
                  }}
                >
                  <Toolbar
                    variant="dense"
                    disableGutters
                    sx={{padding: '10px 0 4px 16px', minHeight: '24px'}}
                  >
                    <CalendarMonthOutlinedIcon
                      sx={{
                        marginRight: '4px',
                        fontSize: 'large',
                      }}
                    />
                    <Typography
                      sx={{fontWeight: 700, fontSize: '14px'}}
                      variant="subtitle2"
                      noWrap={true}
                    >
                      <Timestamp
                        time={
                          new Date(eventDocuments?.event?.start_time * 1000)
                        }
                      />
                      {' - '}
                      {eventDocuments?.event?.title}
                    </Typography>
                  </Toolbar>
                  <TableContainer
                    component={Paper}
                    sx={{
                      boxShadow: 0,
                    }}
                  >
                    <Table
                      size="small"
                      aria-label="meeting documents table"
                      sx={{
                        background: 'var(--card-color)',
                      }}
                    >
                      <TableBody>
                        {eventDocuments?.documents?.map((doc, idx) => (
                          <TableRow
                            hover
                            key={doc.title}
                            onMouseEnter={() =>
                              setHoveredRowIndex(getRowIndex(parentIdx, idx))
                            }
                            onMouseLeave={() => setHoveredRowIndex(null)}
                            onClick={() => {
                              _genLog('click_knowledge_link', {
                                native_id: doc.nativeId,
                                source: doc.source,
                                type: 'document',
                                referrer: 'TODAYS_MEETING_TAB',
                                extra_data: {
                                  index: idx.toString(),
                                  document_url: doc.url,
                                  documents_list: JSON.stringify(
                                    eventDocuments?.documents ?? []
                                  ),
                                },
                              });
                              window.location.href = doc.url;
                            }}
                            style={{
                              cursor: 'pointer',
                              '&:hover': {
                                backgroundColor:
                                  'var(--highlight-background) !important',
                              },
                            }}
                          >
                            <TableCell
                              component="th"
                              scope="row"
                              align="left"
                              style={{
                                color: 'var(--primary-color)',
                                borderBottom: '0px solid var(--divider-color)',
                                padding: '0px 6px 2px 6px',
                              }}
                            >
                              <ListItem sx={{padding: '0px 0px'}}>
                                <ListItemIcon
                                  sx={{
                                    minWidth: '10px',
                                    alignItems: 'center',
                                  }}
                                >
                                  {doc.source === 'attachment' ? (
                                    getAttachmentIcon()
                                  ) : (
                                    <ItemIcon src={doc.documentIconUrl} />
                                  )}
                                </ListItemIcon>
                                <ListItemText
                                  primary={
                                    <Link
                                      href={doc.url}
                                      underline="hover"
                                      sx={{color: 'var(--primary-color)'}}
                                      onClick={() =>
                                        _genLog('click_knowledge_link', {
                                          native_id: doc.nativeId,
                                          source: doc.source,
                                          type: 'document',
                                          referrer: 'TODAYS_MEETING_TAB',
                                          extra_data: {
                                            index: idx.toString(),
                                            document_url: doc.url,
                                          },
                                        })
                                      }
                                      onMouseDown={ev => {
                                        // Handle middle & right clicks
                                        if (
                                          ev.button === 1 ||
                                          ev.button === 2
                                        ) {
                                          _genLog('click_knowledge_link', {
                                            native_id: doc.nativeId,
                                            source: doc.source,
                                            type: 'document',
                                            referrer: 'TODAYS_MEETING_TAB',
                                            extra_data: {
                                              sub_event: 'on_right_click',
                                              index: idx.toString(),
                                              document_url: doc.url,
                                            },
                                          });
                                        }
                                      }}
                                    >
                                      <Typography
                                        variant="caption"
                                        noWrap={true}
                                        sx={{
                                          fontSize: '14px',
                                          width: '230px',
                                          display: 'block',
                                          fontWeight: isNeverSeenHighlighted(
                                            doc.viewedTime,
                                            doc.modifiedTime,
                                            doc.queryType
                                          )
                                            ? 'bold'
                                            : 'normal',
                                        }}
                                      >
                                        {doc.title}
                                      </Typography>
                                    </Link>
                                  }
                                />
                              </ListItem>
                            </TableCell>
                            <TableCell
                              align="right"
                              style={{
                                width: '95px',
                                padding: '0px',
                                color: 'var(--primary-color)',
                                borderBottom: '0px solid var(--divider-color)',
                              }}
                            >
                              {isDocumentTaggingAvailable &&
                                (getBadgeText(doc.tag) ? (
                                  <Chip
                                    variant="outline"
                                    size="sm"
                                    sx={{
                                      '--Chip-minHeight': '15px',
                                      backgroundColor: getBadgeColor(doc.tag),
                                      color: getBadgeTextColor(doc.tag),
                                      ml: '8px',
                                    }}
                                  >
                                    {getBadgeText(doc.tag)}
                                  </Chip>
                                ) : null)}
                            </TableCell>
                            <TableCell
                              align="right"
                              display="inline-flex"
                              style={{
                                width: 160,
                                padding: '0 2px 0 2px',
                                color: 'var(--primary-color)',
                                borderBottom: '0px solid var(--divider-color)',
                              }}
                            >
                              {getTimeMetadata(
                                doc.viewedTime,
                                doc.modifiedTime,
                                doc.modifiedBy
                              )}
                            </TableCell>
                            <TableCell
                              align="right"
                              style={{
                                width: 25,
                                color: 'var(--primary-color)',
                                borderBottom: '0px solid var(--divider-color)',
                                padding: '0 2px 0 2px',
                              }}
                            >
                              {getCopyItem(doc, getRowIndex(parentIdx, idx))}
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </TableContainer>
                </Box>
              )
          )}
        </Box>
      );
    }
    return timelineSections;
  };

  const timelineSections = getTimelineSections();

  return (
    <div id="library-meeting-documents" className="libraryListWrapper">
      {Object.keys(groupedEventsDocuments).length > 0 && (
        <div>
          {timelineSections.upcoming}
          {timelineSections.up_next}
          {timelineSections.past}
        </div>
      )}
    </div>
  );
}

export default memo(LibraryMeetingDocuments);
