import React from 'react';
import Tooltip from '@mui/material/Tooltip';

export default function ThemedTooltip(props) {
  return (
    <Tooltip
      title={props.title}
      placement={props.placement ?? 'top'}
      slotProps={{
        tooltip: {
          style: {
            backgroundColor: 'var(--tooltip-background-color)',
            borderRadius: '12px',
            color: 'var(--tooltip-text-color)',
            fontSize: '14px',
            fontWeight: 400,
            lineHeight: '20px',
            fontFamily: 'SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif',
          },
        },
      }}
      onOpen={props.onOpen}
      onClose={props.onClose}
    >
      {props.children}
    </Tooltip>
  );
}
