import React from 'react';
import styled from 'styled-components';
import Tippy from '@tippyjs/react';

const NewTabPapercutsButton = styled.button`
  border-radius: 50%;
  border: 0;
  cursor: pointer;
  padding: 8px 8px 6px;
  outline: 0;
  opacity: 0.9;
  &:hover {
    opacity: 1;
  }
  background: var(--overlay-background);
`;

const NewTabPapercutsButtonImage = styled.img`
  width: 18px;
  height: 18px;
  filter: var(--black-icon-filter);
`;

const NewTabPapercutsWrapper = styled.div`
  position: absolute;
  color: var(--primary-color);
  &:hover {
    opacity: 1;
    background-color: var(--overlay-background);
  }
  background-color: var(--overlay-background-opaque);
  border-radius: 10%;
  margin: 4px 8px;
  left: 0;
  bottom: 150px;
`;

