import Chip from '@mui/material/Chip';
import React, {memo, useState} from 'react';
import Table from '@mui/material/Table';
import TableContainer from '@mui/material/TableContainer';
import Paper from '@mui/material/Paper';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import Tooltip from '@mui/material/Tooltip';
import Tippy from '@tippyjs/react';
import CircleIcon from '@mui/icons-material/Circle';
import Link from '@mui/material/Link';
import {createTheme, ThemeProvider} from '@mui/material/styles';
import genLog from '../genLog.js';
import withEntryPointParam from './withEntryPointParam.js';
import useGK from '../useGK.js';
import ThemedTooltip from '../ThemedTooltip.js';
import formatRelativeTime from '../formatRelativeTime.js';

const COPY_DIFF_NUMBER_TOOLTIP = 'Copy to clipboard.';

// augmentColor() function relies on theme being created first
let theme = createTheme({});
theme = createTheme(createTheme({}), {
  // Custom colors created with augmentColor go here
  palette: {
    mode: 'dark',
    shuttleGrey: theme.palette.augmentColor({
      color: {
        main: '#65676B',
      },
      name: 'shuttleGrey',
    }),
    grey: theme.palette.augmentColor({
      color: {
        main: '#808080',
      },
      name: 'grey',
    }),
    purple: theme.palette.augmentColor({
      color: {
        main: '#800080',
      },
      name: 'purple',
    }),
    magenta: theme.palette.augmentColor({
      color: {
        main: '#C000C0',
      },
      name: 'magenta',
    }),
    orange: theme.palette.augmentColor({
      color: {
        main: '#FF9831',
      },
      name: 'orange',
    }),
    blue: theme.palette.augmentColor({
      color: {
        main: '#3E93F8',
      },
      name: 'blue',
    }),
  },
});

// Let's start with just the common status. We can add more later as needed.
// https://www.internalfb.com/code/www/[master]/html/intern/js/diffs/DiffsDiffStatus.js
export function getStatusLabel(diff) {
  const status = diff.phabricator_diff_status_enum;
  const landJobStatus = diff.land_job_status;
  const postLandReviewType = diff.post_land_review_type;
  const privacyReviewStatus = diff.post_land_review_info?.privacy_review_status;
  const stewardReviewInfoStatus = diff.steward_review_info?.overall_status;

  if (landJobStatus === 'LAND_JOB_RUNNING') {
    return 'Landing';
  }

  if (landJobStatus === 'LAND_RECENTLY_SUCCEEDED') {
    return 'Committing';
  }

  if (landJobStatus === 'LAND_RECENTLY_FAILED') {
    return 'Recently Failed To Land';
  }

  if (postLandReviewType === 'PRIVACY_REVIEW') {
    return privacyReviewStatus === 'ACKNOWLEDGEMENT_NEEDED'
      ? 'Needs Confirmation'
      : 'Needs Pre-screener';
  }

  if (postLandReviewType === 'FINAL_REVIEW') {
    return 'Needs Final Review';
  }

  if (postLandReviewType === 'OWNER_REVIEW') {
    return 'Needs Owner Review';
  }

  if (stewardReviewInfoStatus === 'AWAITING') {
    return 'Awaiting Steward Review';
  }

  switch (status) {
    case 'ACCEPTED':
      return 'Accepted';
    case 'NEEDS_REVIEW':
      return 'Needs Review';
    case 'CHANGES_PLANNED':
      return 'Changes Planned';
    case 'NEEDS_REVISION':
      return 'Waiting For Author';
    case 'CLOSED':
      return 'Committed';
    case 'ABANDONED':
      return 'Abandoned';
    case 'REVERTED':
      return 'Reverted';
    case 'IN_PREPARATION':
      return 'Unpublished';
    case 'RESERVED':
      return 'Reserved';
    default:
      return 'Unknown';
  }
}

function getStatusColor(diff) {
  const status = diff.phabricator_diff_status_enum;
  const postLandReviewType = diff.post_land_review_type;
  const stewardReviewInfoStatus = diff.steward_review_info?.overall_status;

  if (postLandReviewType != null) {
    return 'purple';
  }
  if (stewardReviewInfoStatus === 'AWAITING') {
    return 'orange';
  }
  switch (status) {
    case 'ACCEPTED':
      return 'success';
    case 'NEEDS_REVIEW':
      return 'warning';
    case 'CHANGES_PLANNED':
    case 'NEEDS_REVISION':
      return 'magenta';
    case 'REVERTED':
      return 'grey';
    case 'ABANDONED':
    case 'CLOSED':
    case 'IN_PREPARATION':
    default:
      return 'shuttleGrey';
  }
}

function DiffTable(props) {
  const isRevamp = useGK('intern_new_tab_revamp');
  const {diffs} = props;
  const [copyToClipboardTooltip, setCopyToClipboardTooltip] = useState(
    COPY_DIFF_NUMBER_TOOLTIP
  );
  const resetCopyTooltip = () =>
    setCopyToClipboardTooltip(COPY_DIFF_NUMBER_TOOLTIP);
  const setCopiedTooltip = diffNumber =>
    setCopyToClipboardTooltip(`Copied ${diffNumber}.`);

  if (diffs == null || diffs.length === 0) {
    return null;
  }

  const getAvatarPicture = diff => (
    <ThemedTooltip title={diff.intern_author?.name}>
      <Avatar
        sx={{width: 20, height: 20}}
        alt={diff.intern_author?.name}
        src={diff.intern_author?.profile_pic_uri}
      />
    </ThemedTooltip>
  );

  const getDiffNumberCell = diff => (
    <TableCell
      style={{
        padding: '8px',
        color: 'var(--primary-color)',
        borderRadius: '6px 0 0 6px',
        borderBottom: 'none',
        gap: '8px',
        display: 'flex',
        flexDirection: 'row',
      }}
    >
      {getAvatarPicture(diff)}
      <Link
        underline="hover"
        sx={{color: 'var(--primary-color)'}}
        onClick={event => {
          event.stopPropagation();
          navigator.clipboard.writeText(diff.number_with_prefix);
          setCopiedTooltip(diff.number_with_prefix);
          genLog('diffs_copy_number_click', JSON.stringify(diff));
        }}
      >
        <ThemedTooltip
          title={copyToClipboardTooltip}
          onOpen={resetCopyTooltip}
          onClose={resetCopyTooltip}
          placement="top"
          arrow
        >
          <Typography
            variant="caption"
            display="inline"
            gutterBottom
            noWrap
            sx={{
              fontWeight: diff.is_unread ? '700' : '400',
              fontSize: '14px',
              lineHeight: '20px',
              fontFamily: 'SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif',
            }}
          >
            {diff.number_with_prefix}
          </Typography>
        </ThemedTooltip>
      </Link>
    </TableCell>
  );

  const getDiffTitleCell = diff => (
    <TableCell
      component="th"
      scope="row"
      style={{
        padding: '0 8px 0 0',
        color: 'var(--primary-color)',
        borderBottom: 'none',
        maxWidth: '400px',
      }}
    >
      <Link
        href={withEntryPointParam(diff.uri)}
        underline="hover"
        sx={{color: 'var(--primary-color)', maxWidth: '400px'}}
        onClick={() => genLog('diffs_diff_link_click', JSON.stringify(diff))}
      >
        <ThemedTooltip title={diff.diff_title} placement="top-start">
          <Typography
            variant="caption"
            noWrap={true}
            sx={{
              fontWeight: '400',
              fontSize: '14px',
              lineHeight: '20px',
              fontFamily: 'SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif',
              textOverflow: 'ellipsis',
              overflow: 'hidden',
              display: 'inline-block',
              maxWidth: '530px',
            }}
          >
            {diff.diff_title}
          </Typography>
        </ThemedTooltip>
      </Link>
    </TableCell>
  );

  const getDiffStatusCell = diff => (
    <TableCell
      style={{
        padding: '0 8px 0 0',
        color: 'var(--primary-color)',
        borderBottom: 'none',
        textAlign: 'center',
      }}
    >
      <Chip
        label={getStatusLabel(diff)}
        color={getStatusColor(diff)}
        size="small"
        sx={{
          fontSize: '10px',
          padding: 0,
          height: '20px',
        }}
      />
    </TableCell>
  );

  const getDiffUpdateTimeCell = diff => (
    <TableCell
      align="right"
      style={{
        padding: '0 8px 0 0',
        color: 'var(--primary-color)',
        borderBottom: 'none',
      }}
    >
      <Typography
        variant="caption"
        display="inline"
        gutterBottom
        noWrap
        sx={{
          fontSize: '12px',
          lineHeight: '16px',
          fontWeight: '400',
          fontFamily: 'SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif',
          color: 'var(--secondary-text-color)',
        }}
      >
        {diff.updated_time && formatRelativeTime(diff.updated_time * 1000)}
      </Typography>
    </TableCell>
  );

  const getNotificationIsUnreadCell = diff => (
    <TableCell
      align="right"
      style={{
        padding: '0 8px 0 0',
        borderBottom: 'none',
        borderRadius: '0 6px 6px 0',
      }}
    >
      {diff.is_unread && (
        <ThemedTooltip title="New update">
          <CircleIcon
            color="blue"
            sx={{fontSize: 10, verticalAlign: 'middle'}}
          />
        </ThemedTooltip>
      )}
    </TableCell>
  );

  if (isRevamp) {
    return (
      <ThemeProvider theme={theme}>
        <div className="diffTable">
          <TableContainer
            component={Paper}
            sx={{
              background: 'var(--widget-background-color)',
              boxShadow: 'none',
            }}
          >
            <Table
              aria-label="Diffs"
              sx={{
                width: '100%',
                '& .MuiTableRow-root:hover': {
                  backgroundColor: 'var(--hover-overlay-color)',
                },
                '& .MuiTableRow-root:active': {
                  backgroundColor: 'var(--focus-overlay-color)',
                },
              }}
            >
              <TableBody>
                {diffs.filter(Boolean).map(diff => (
                  <TableRow
                    key={diff.number_with_prefix}
                    onClick={() => {
                      genLog('diffs_diff_link_click', JSON.stringify(diff));
                      window.location.href = withEntryPointParam(diff.uri);
                    }}
                    style={{
                      cursor: 'pointer',
                    }}
                  >
                    {getDiffNumberCell(diff)}
                    {getDiffTitleCell(diff)}
                    {getDiffStatusCell(diff)}
                    {getDiffUpdateTimeCell(diff)}
                    {getNotificationIsUnreadCell(diff)}
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      </ThemeProvider>
    );
  }

  return (
    <ThemeProvider theme={theme}>
      <TableContainer
        component={Paper}
        sx={{
          background: 'var(--overlay-background)',
          overflowX: 'hidden',
        }}
        className="diffTable"
      >
        <Table
          aria-label="Diffs"
          sx={{
            '& .MuiTableRow-root:hover': {
              backgroundColor: 'var(--highlight-background)',
            },
          }}
        >
          <TableBody>
            {diffs.filter(Boolean).map(diff => (
              <TableRow
                key={diff.number_with_prefix}
                onClick={() => {
                  genLog('diffs_diff_link_click', JSON.stringify(diff));
                  window.location.href = withEntryPointParam(diff.uri);
                }}
                style={{
                  cursor: 'pointer',
                  height: '32px',
                }}
              >
                <TableCell
                  align="right"
                  style={{
                    padding: '0 4px 0 20px',
                    color: 'var(--primary-color)',
                    borderBottom: '1px solid var(--divider-color)',
                  }}
                >
                  {diff.is_unread && (
                    <Tooltip title="New update">
                      <CircleIcon color="blue" sx={{fontSize: 10}} />
                    </Tooltip>
                  )}
                </TableCell>
                <TableCell
                  align="right"
                  style={{
                    padding: '0 4px 0 4px',
                    borderBottom: '1px solid var(--divider-color)',
                  }}
                >
                  <Tippy content={diff.intern_author?.name}>
                    <Avatar
                      sx={{width: 18, height: 18}}
                      alt={diff.intern_author?.name}
                      src={diff.intern_author?.profile_pic_uri}
                    />
                  </Tippy>
                </TableCell>
                <TableCell
                  style={{
                    padding: 0,
                    color: 'var(--primary-color)',
                    borderBottom: '1px solid var(--divider-color)',
                  }}
                >
                  <Link
                    underline="hover"
                    sx={{color: 'var(--primary-color)'}}
                    onClick={event => {
                      event.stopPropagation();
                      navigator.clipboard.writeText(diff.number_with_prefix);
                      setCopiedTooltip(diff.number_with_prefix);
                      genLog('diffs_copy_number_click', JSON.stringify(diff));
                    }}
                  >
                    <Tooltip
                      title={copyToClipboardTooltip}
                      onOpen={resetCopyTooltip}
                      onClose={resetCopyTooltip}
                      placement="top"
                      arrow
                    >
                      <Typography
                        variant="caption"
                        display="inline"
                        gutterBottom
                        noWrap
                        sx={{fontWeight: diff.is_unread ? 'bold' : 'normal'}}
                      >
                        {diff.number_with_prefix}
                      </Typography>
                    </Tooltip>
                  </Link>
                </TableCell>
                <TableCell
                  component="th"
                  scope="row"
                  style={{
                    padding: '0px 2px 0px 4px',
                    color: 'var(--primary-color)',
                    borderBottom: '1px solid var(--divider-color)',
                  }}
                >
                  <Link
                    href={withEntryPointParam(diff.uri)}
                    underline="hover"
                    sx={{color: 'var(--primary-color)'}}
                    onClick={() =>
                      genLog('diffs_diff_link_click', JSON.stringify(diff))
                    }
                  >
                    <Tooltip title={diff.diff_title} placement="top-start">
                      <Typography
                        variant="caption"
                        noWrap={true}
                        sx={{
                          fontSize: '12px',
                          display: 'block',
                          maxWidth: '356px',
                        }}
                      >
                        {diff.diff_title}
                      </Typography>
                    </Tooltip>
                  </Link>
                </TableCell>
                <TableCell
                  style={{
                    padding: 0,
                    color: 'var(--primary-color)',
                    borderBottom: '1px solid var(--divider-color)',
                  }}
                >
                  <Chip
                    label={getStatusLabel(diff)}
                    color={getStatusColor(diff)}
                    size="small"
                    sx={{
                      maxWidth: '103px',
                      fontSize: '10px',
                      padding: 0,
                      height: '20px',
                    }}
                  />
                </TableCell>
                <TableCell
                  align="right"
                  style={{
                    padding: '0 12px 0 4px',
                    color: 'var(--primary-color)',
                    borderBottom: '1px solid var(--divider-color)',
                  }}
                >
                  <Typography
                    variant="caption"
                    display="inline"
                    gutterBottom
                    noWrap
                    sx={{
                      fontSize: '10px',
                      maxWidth: '42px',
                    }}
                  >
                    {diff.updated_time && formatRelativeTime(diff.updated_time * 1000)}
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </ThemeProvider>
  );
}

export default memo(DiffTable);
