import React from 'react';
import styled from 'styled-components';
import Todo from './Todo.js';
import Conversations from './Conversations.js';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';

const KEY = 'calendarext/settings/';

const Wrapper = styled.div`
  align-self: flex-end;
  padding: 0 16px 16px 0;
  .searchFocused & {
    display: none;
  }
  position: absolute;
  bottom: 0;
  right: 0;
  max-height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: fixed;
  z-index: 2;
`;

export default function RightPane() {
  const [tabs, setTabs] = useStateWithSyncStorage(KEY, []);
  let actualTabs = tabs;
  if (actualTabs == null) {
    actualTabs = [];
  } else if (!Array.isArray(actualTabs)) {
    actualTabs = [actualTabs];
  }
  return (
    <Wrapper>
      <Conversations
        isOpen={actualTabs.indexOf('conversations') >= 0}
        onOpen={() => setTabs([...actualTabs, 'conversations'])}
        onClose={() => setTabs(actualTabs.filter(t => t !== 'conversations'))}
      />
      <Todo
        isOpen={actualTabs.indexOf('todo') >= 0}
        onOpen={() => setTabs([...actualTabs, 'todo'])}
        onClose={() => setTabs(actualTabs.filter(t => t !== 'todo'))}
      />
    </Wrapper>
  );
}
