export function getDescription(str) {
  if (str.match(/^[Dd](?<id>[0-9]{3,})$/)) {
    return 'Goes to a diff in the diffs tool';
  }
  if (str.match(/^(T|t|#){1,2}(?<id>[0-9]{3,})$/)) {
    return 'Goes to a task in the tasks tool';
  }
  return COMMANDS[str.toLowerCase()];
}
