import {useState, useEffect} from 'react';
import {genCachedGraphQL} from './../GraphQL.js';
import useStateWithSyncStorage from '../useStateWithSyncStorage';
import {storageKeys} from '../LocalStorage.js';

export default function useDiffsEnabled() {
  const [isOptIn, setIsOptIn] = useState(null);
  const [diffsEnabled, setDiffsEnabled] = useStateWithSyncStorage(
    storageKeys.diffs,
    null
  );

  // Check if user is eligible for Diffs auto opt-in
  useEffect(() => {
    genCachedGraphQL('6873996409377672', {}, 300, data => {
      setIsOptIn(!!data.intern_new_tab_diffs_eligibility);
    });
  }, []);

  // Auto enable Diffs if user should be opt-in and has not changed the settings
  useEffect(() => {
    if (isOptIn === true && diffsEnabled == null) {
      setDiffsEnabled(true);
    }
  }, [isOptIn, diffsEnabled, setDiffsEnabled]);

  return [diffsEnabled === true, setDiffsEnabled];
}
