// https://fburl.com/code/mjbyzyuv
const INTERN_NEW_TAB = 37;

export default function withEntryPointParam(baseUrl) {
  const url = new URL(baseUrl);
  url.searchParams.set('entry_point', INTERN_NEW_TAB);
  return url.toString();
}
