import React, {memo, useEffect} from 'react';
import {genCachedGraphQL} from './../GraphQL.js';
import DiffTable from './DiffTable.js';
import Spinner from '../Spinner.js';
import EmptyResultMessage from './EmptyResultMessage.js';
import genLog from '../genLog.js';

// bunnylol graphiql -> Load Query to see the query content
const INTERN_NEW_TAB_DIFFS_SEARCH_QUERY_ID = '7436084823151733';

// GraphQL Enum https://fburl.com/code/2km0vqsd
const SEARCH_TYPE_CUSTOM = 'CUSTOM';

function getQueryVariables(searchType) {
  if (searchType === SEARCH_TYPE_CUSTOM) {
    return null;
  }
  return {
    args: {
      search_type: searchType,
    },
  };
}

function getDiffsFromQueryResults(data, searchType) {
  if (searchType === SEARCH_TYPE_CUSTOM) {
    return data.phabricator_diff_custom_search_query.phabricator_diffs.nodes;
  }
  return data.xfb_phabricator_diffs_in_intern_new_tab_diffs?.phabricator_diffs;
}

function DiffsSearch(props) {
  const {title, isVisible, searchType, customQueryID} = props;
  const [diffs, setDiffs] = React.useState(null);
  const queryID =
    searchType === SEARCH_TYPE_CUSTOM
      ? customQueryID
      : INTERN_NEW_TAB_DIFFS_SEARCH_QUERY_ID;

  useEffect(() => {
    if (isVisible) {
      genCachedGraphQL(queryID, getQueryVariables(searchType), 60, data => {
        const results = getDiffsFromQueryResults(data, searchType);
        setDiffs(results);
        genLog(
          'diffs_search_results_load',
          JSON.stringify({title, searchType, queryID, count: results.length})
        );
      });
    }
  }, [queryID, searchType, isVisible, title]);

  if (!isVisible) {
    return null;
  }

  if (diffs == null) {
    return <Spinner />;
  }

  return diffs.length > 0 ? (
    <DiffTable diffs={diffs} />
  ) : (
    <EmptyResultMessage />
  );
}

export default memo(DiffsSearch);
