import styled from 'styled-components';
import React, { useState, useEffect } from 'react';
import genLog from './genLog.js';
import genJoinInVRLink from './genJoinInVRLink.js';
import useGK from './useGK.js';

const ButtonGroup = styled.div`
  display: flex;
  flex-direction: row;
  gap: 8px;
`;

const Button = styled.a`
  display: flex;
  flex: 1;
  justify-content: center;
  background-color: var(--highlight-color);
  padding: 8px 12px;
  border-radius: 8px;
  border: 2px solid var(--divider-color);
  font-size: 14px;
  font-weight: 700;
  line-height: 20px;
  font-family: SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif;
  &:hover {
    box-shadow: inset 0 0 0 100px var(--hover-overlay-color);
    text-decoration: none;
  }
  &:active {
    box-shadow: inset 0 0 0 100px var(--focus-overlay-color);
    text-decoration: none;
  }
`;

export default function JoinMeetingButton(props) {
  const conference = props.event.conference;
  const conferenceLink = conference ? conference.joinable_link : null;
  const [joinInVRLink, setJoinInVRLink] = useState(conferenceLink);
  const shouldIncludeJoinInVRLink =
    conference?.mode === 'WORKROOMS' || conference?.mode === 'ZOOM';
  const showJoinInVR = useGK('workrooms_join_in_vr_intern_tab');

  useEffect(() => {
    if (conferenceLink) {
      genJoinInVRLink(
        conferenceLink,
        shouldIncludeJoinInVRLink,
        'INTERN_TAB_MEETING_POPOVER',
        setJoinInVRLink
      );
    }
  }, [conferenceLink, shouldIncludeJoinInVRLink]);

  if (!conferenceLink) {
    return null;
  }

  return (
    <ButtonGroup>
      <Button href={conferenceLink} onClick={() => genLog('click_join_meeting')}>
        <span style={{color: '#ffffff'}}>Join Meeting</span>
      </Button>
      {joinInVRLink && showJoinInVR && (
        <Button
          href={joinInVRLink}
          onClick={() => genLog('click_join_vr_meeting')}
        >
          <span style={{color: '#ffffff'}}>Join in VR</span>
        </Button>
      )}
    </ButtonGroup>
  );
}
