import React from 'react';
import styled, {css} from 'styled-components';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys} from './LocalStorage.js';
import {useBookmarkList} from './genBookmarks.js';

const BookmarksList = styled.div`
  align-items: center;
  display: flex;
  font-family: 'SF Pro Text', Helvetica, Arial, 'Noto Sans', sans-serif;
  width: 100%;
  height: 40px;
  grid-column: 1 / -1;
  grid-row: 1;
  position: relative;
  z-index: 10;
`;

const BookmarkFolder = styled.div`
  position: relative;
`;

const BookmarkFolderItems = styled.div`
  display: none;
  position: absolute;
  margin-top: 2px;
  flex-direction: column;
  max-height: calc(100vh - 100px);
  overflow-y: auto;
  overflow-x: hidden;
  width: 240px;
  border-radius: 8px;
  transition: all 0.5s ease-in-out;
  box-shadow: 0 0 0 1px #fff2, 0 0 12px #0002;
  ${BookmarkFolder}:hover & {
    display: flex;
  }
  ${props =>
    props.isRight &&
    css`
      right: 16px;
    `};
`;

const BookmarkButton = styled.a`
  padding: 12px;
  display: flex;
  align-items: center;
  min-width: 0;
  color: var(--primary-color);
  background-color: var(--overlay-background);
  &:hover {
    text-decoration: none;
  }
`;

const BookmarkButtonText = styled.span`
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 240px;
  overflow: hidden;
`;

const BookmarkFolderImage = styled.img`
  margin-right: 8px;
  width: 14px;
  height: 14px;
  filter: var(--icon-filter);
`;

const Spacer = styled.div`
  background-color: var(--overlay-background);
  flex-grow: 1;
  flex-shrink: 1;
  padding: 12px 0;
`;

const Space = styled.div`
  background-color: var(--overlay-background);
  width: 12px;
  padding: 12px 0;
`;

function Bookmark(props) {
  return (
    <BookmarkButton href={props.url}>
      <BookmarkButtonText>{props.title}</BookmarkButtonText>
    </BookmarkButton>
  );
}

function BookmarkFolderComponent(props) {
  const bookmarks = props.bookmarks.filter(
    bm => !!bm.url && bm.title.length > 0
  );
  return (
    <BookmarkFolder>
      <BookmarkButton>
        <BookmarkFolderImage src="/icon-folder.png" />
        <BookmarkButtonText>{props.title}</BookmarkButtonText>
      </BookmarkButton>
      <BookmarkFolderItems isRight={props.isRight}>
        {bookmarks.map((bm, idx) => (
          <BookmarkButton href={bm.url} key={idx}>
            {bm.title}
          </BookmarkButton>
        ))}
      </BookmarkFolderItems>
    </BookmarkFolder>
  );
}

function BookmarkBar(props) {
  const bookmarks = useBookmarkList();
  const [bookmarksEnabled] = useStateWithSyncStorage(
    storageKeys.bookmarks,
    false
  );
  if (!bookmarks || bookmarks === [] || bookmarksEnabled === false) {
    return null;
  }
  const inlineBookmarks = bookmarks.slice(0, 20);
  const extraBookmarks = bookmarks.filter(bm => !!bm.url);
  return (
    <BookmarksList>
      <Space>&nbsp;</Space>
      {inlineBookmarks.map(bm => {
        if (!bm.url) {
          return (
            <BookmarkFolderComponent title={bm.title} bookmarks={bm.children} />
          );
        }
        return <Bookmark key={bm.id} title={bm.title} url={bm.url} />;
      })}
      <Spacer>&nbsp;</Spacer>
      {extraBookmarks.length > 0 ? (
        <BookmarkFolderComponent
          isRight={true}
          title={'All'}
          bookmarks={extraBookmarks}
        />
      ) : null}
      <Space>&nbsp;</Space>
    </BookmarksList>
  );
}

export default BookmarkBar;
