import styled from 'styled-components';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys} from './LocalStorage.js';
import Tippy from '@tippyjs/react';

const BgmAttributionWrapper = styled.a`
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  flex-direction: column;
  margin: 16px 8px;
  color: #ffffff;
  font-size: 14px;
  cursor: pointer;
  text-decoration: none;
`;
const BgmAttributionTitleItem = styled.div`
  line-height: 20px;
  color: #ffffff;
  font-size: 16px;
  width: 40ch;
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
`;
const BgmAttributionArtistItem = styled.div`
  line-height: 20px;
  color: #ffffff;
  font-size: 14px;
  width: 40ch;
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
`;
const BgmAttributionLocationItem = styled.div`
  line-height: 20px;
  color: #ffffff;
  font-size: 12px;
  width: 40ch;
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-weight: 600;
`;
const BgmAttributionWorkplaceGroupItem = styled.div`
  line-height: 20px;
  color: #ffffff;
  font-size: 12px;
  font-style: italic;
  weight: 400;
`;

function BgmImageAttributionDialog(props) {
  return (
    <>
      <BgmAttributionWorkplaceGroupItem>
        Artwork of the Day c/o Meta Open Arts
      </BgmAttributionWorkplaceGroupItem>
    </>
  );
}

export default function BackgroundImageAttribution() {
  const [background] = useStateWithSyncStorage(
    storageKeys.background,
    'open_arts'
  );
  const [openArtsImage] = useStateWithSyncStorage(
    storageKeys.openArtsImage,
    null
  );

  if (background !== 'open_arts') {
    return null;
  }

  return (
    <Tippy content={<BgmImageAttributionDialog />}>
      <BgmAttributionWrapper href="https://fb.workplace.com/groups/fbopenarts/">
        <BgmAttributionTitleItem>
          {openArtsImage?.title}
        </BgmAttributionTitleItem>
        <BgmAttributionArtistItem>
          {openArtsImage?.artist_name && `by ${openArtsImage?.artist_name}`}
        </BgmAttributionArtistItem>
        <BgmAttributionLocationItem>
          {openArtsImage?.building}
        </BgmAttributionLocationItem>
      </BgmAttributionWrapper>
    </Tippy>
  );
}
