import React from 'react';
import styled from 'styled-components';
import genLog from '../genLog.js';

const DIFFS_DASHBOARD_URL =
  'https://www.internalfb.com/diffs?entry_point=INTERN_NEW_TAB';

const Button = styled.a`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3px 8px;
  &:hover {
    border-radius: 6px;
    background-color: rgba(255, 255, 255, 0.5);
    text-decoration: none;
  }
`;

const ButtonIcon = styled.img`
  width: 16px;
  height: 16px;
  filter: var(--black-icon-filter);
  margin-left: 4px;
`;

function GoToDashboardButton(props) {
  return (
    <div className="overlayBackground primaryColor">
      <Button
        href={DIFFS_DASHBOARD_URL}
        target="_blank"
        onClick={() =>
          genLog('diffs_dashboard_link_click', 'go_to_diffs_button')
        }
      >
        Go to Diffs
        <ButtonIcon src="/share_external.svg" />
      </Button>
    </div>
  );
}

export default GoToDashboardButton;
