import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys, DEFAULT_24_HOUR} from './LocalStorage.js';
import React from 'react';
import {format} from 'date-fns';
import {tz} from '@date-fns/tz';

function Timestamp(props) {
  const [use24Hour] = useStateWithSyncStorage(
    storageKeys.use24Hour,
    DEFAULT_24_HOUR
  );
  const [background] = useStateWithSyncStorage(
    storageKeys.background,
    // Default background
    'open_arts'
  );
  const time = format(
    props.time ?? new Date(),
    use24Hour ? 'HH:mm' : 'h:mm a',
    {in: tz(props.tz)}
  );
  if (props.split) {
    const parts = time.split(' ');
    return (
      <span>
        {parts[0]}
        <span
          className={
            'timeAMPM ' +
            (background === 'open_arts' ? 'quaternaryColor' : 'primaryColor')
          }
        >
          {parts.length >= 2 ? parts[1] : null}
        </span>
      </span>
    );
  }
  return time;
}

export default Timestamp;
