import styled from 'styled-components';
import React from 'react';

const Button = styled.a`
  display: flex;
  justify-content: center;
  background-color: var(--secondary-button-color);
  padding: 8px 12px;
  border-radius: 8px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  font-family: SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif;
  &:hover {
    box-shadow: inset 0 0 0 100px var(--hover-overlay-color);
    text-decoration: none;
  }
  &:active {
    box-shadow: inset 0 0 0 100px var(--focus-overlay-color);
    text-decoration: none;
  }
`;

export default function OneOneButton(props) {
  const conversationUri = props.event?.conversation_uri;

  if (conversationUri == null) {
    return null;
  }

  return <Button href={conversationUri}>View 1:1 Notes</Button>;
}
