import React from 'react';
import styled from 'styled-components';
import {toast} from 'react-toastify';

const ToastCheckMark = styled.img`
  height: 15px;
  width: 15px;
  padding-right: 10px;
`;

const ToastContentWrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const ToastContentTextArea = styled.div`
  display: flex:
  flex-direction: vertical;
`;

const ToastTitle = styled.div`
  font-weight: bold;
  padding-bottom: 4px;
`;

const ToastContents = ({title, description}) => {
  return (
    <ToastContentWrapper>
      <ToastCheckMark src="/green_checkmark.png" alt="green checkmark" />
      <ToastContentTextArea>
        <ToastTitle>{title}</ToastTitle>
        <div>{description}</div>
      </ToastContentTextArea>
    </ToastContentWrapper>
  );
};

export const openRecentlyViewedLibraryActionToast = (title, description) => {
  toast(<ToastContents title={title} description={description} />, {
    hideProgressBar: true,
  });
};

export const openLinkCopiedToast = title => {
  openRecentlyViewedLibraryActionToast(title, 'Link copied!');
};
