import {useEffect} from 'react';
import {useMutation, useQuery, useQueryClient} from 'react-query';

import {storageKeys} from '../LocalStorage.js';
import useStateWithSyncStorage from '../useStateWithSyncStorage';
import {
  getCollections,
  getExistingCollectionItems,
  getRecentlyViewed,
  getActiveDocuments,
  _saveToCollection,
} from './libraryRecentlyViewed.utils';

export const useCollections = () => {
  return useQuery('COLLECTION_OPTIONS', () => getCollections(), {
    staleTime: Infinity,
    notifyOnChangeProps: 'tracked',
    placeholderData: [],
  });
};

export const useRecentlyViewed = () => {
  const [theme] = useStateWithSyncStorage(storageKeys.theme);
  return useQuery('RECENTLY_VIEWED', () => getRecentlyViewed(theme), {
    staleTime: Infinity,
    notifyOnChangeProps: 'tracked',
    placeholderData: [],
  });
};

export const useActiveDocuments = () => {
  const [theme] = useStateWithSyncStorage(storageKeys.theme);
  return useQuery('ACTIVE_DOCUMENTS', () => getActiveDocuments(theme), {
    staleTime: Infinity,
    notifyOnChangeProps: 'tracked',
    placeholderData: [],
  });
};

export const useLibraryEnabled = () => {
  const [libraryEnabled] = useStateWithSyncStorage(storageKeys.library, true);
  return libraryEnabled;
};

export const useExistingCollectionItems = collectionId => {
  return useQuery(
    ['EXISTING_COLLECTION_ITEMS', collectionId],
    async () => getExistingCollectionItems(collectionId),
    {
      placeholderData: new Set(),
      enabled: collectionId !== undefined,
      staleTime: Infinity,
      notifyOnChangeProps: 'tracked',
    }
  );
};

export const usePrefetchExistingCollectionItems = () => {
  const {data: collections} = useCollections();
  const queryClient = useQueryClient();

  useEffect(() => {
    if (collections && collections.length > 0) {
      collections.forEach(collection => {
        queryClient.prefetchQuery(
          ['EXISTING_COLLECTION_ITEMS', collection.id],
          () => getExistingCollectionItems(collection.id),
          {
            staleTime: Infinity,
            placeholderData: new Set(),
          }
        );
      });
    }
  }, [collections, queryClient]);
};

export const useAddToCollection = () => {
  const queryClient = useQueryClient();
  return useMutation(
    ({collection, rv}) => _saveToCollection(rv, collection.id),
    {
      onMutate: ({collection, rv}) => {
        // Optimisitically update our cache
        queryClient.setQueryData(
          ['EXISTING_COLLECTION_ITEMS', collection.id],
          existing => {
            const newSet = new Set([...existing, rv.nativeId]);
            return newSet;
          }
        );
      },
      onSuccess: (_, {collection}) => {
        // When it succeeds, invalidate to refetch, just to be sure
        queryClient.invalidateQueries([
          'EXISTING_COLLECTION_ITEMS',
          collection.id,
        ]);
      },
    }
  );
};
