import React, {useState, useEffect} from 'react';
import genLog from './genLog.js';
import Button from '@mui/material/Button';
import styled from 'styled-components';
import ButtonGroup from '@mui/material/ButtonGroup';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import VideocamIcon from '@mui/icons-material/Videocam';
import ClickAwayListener from '@mui/material/ClickAwayListener';
import Grow from '@mui/material/Grow';
import Paper from '@mui/material/Paper';
import Popper from '@mui/material/Popper';
import MenuItem from '@mui/material/MenuItem';
import MenuList from '@mui/material/MenuList';
import useGK from './useGK.js';
import genJoinInVRLink from './genJoinInVRLink.js';

const StyledButton = styled.button`
  align-items: center;
  background-color: var(--highlight-color);
  color: #ffffff;
  padding: 4px 16px;
  cursor: pointer;
  border: 0;
  border-radius: 8px;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  display: flex;
  &:hover {
    box-shadow: inset 0 0 0 100px var(--hover-overlay-color);
  }
  &:active {
    box-shadow: inset 0 0 0 100px var(--focus-overlay-color);
  }
`;

const StyledSecondaryButton = styled(StyledButton)`
  border-radius: 8px;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  padding: 4px 8px;
`;

const StyledButtonText = styled.span`
  font-size: 14px;
  font-weight: 700;
  line-height: 20px;
  margin-left: 8px;
`;

const StyledButtonImage = styled.img`
  width: 16px;
  height: 16px;
`;

const StyledButtonGroup = styled.div`
  display: flex;
  justify-content: space-between;
  gap: 2px;
`;

export default function JoinUpNextMeetingButton(props) {
  const isRevamp = useGK('intern_new_tab_revamp');
  const options = [];
  const [open, setOpen] = React.useState(false);
  const anchorRef = React.useRef(null);
  const [selectedIndex, setSelectedIndex] = React.useState(0);

  const showJoinInVR = useGK('workrooms_join_in_vr_intern_tab');

  const conference = props.event.conference;

  const conferenceLink = conference ? conference.joinable_link : null;

  const [joinInVRLink, setJoinInVRLink] = useState(conferenceLink);

  const shouldIncludeJoinInVRLink =
    conference?.mode === 'WORKROOMS' || conference?.mode === 'ZOOM';

  useEffect(() => {
    genJoinInVRLink(
      conferenceLink,
      shouldIncludeJoinInVRLink,
      'INTERN_TAB_NEXT_MEETING',
      setJoinInVRLink
    );
  }, [conferenceLink, shouldIncludeJoinInVRLink]);

  if (conferenceLink == null) {
    return null;
  }

  const onClickConferenceLink = () => {
    genLog('click_join_meeting');
  };

  const onJoinInVRClick = () => {
    genLog('click_join_meeting');
  };

  options.push({
    title: 'Join',
    href: conferenceLink,
    onClick: onClickConferenceLink,
  });

  if (props.event.join_meeting_uri != null) {
    options.push({
      title: 'Join from conference room',
      href: props.event.join_meeting_uri,
      onClick: onClickConferenceLink,
    });
  }

  if (joinInVRLink && showJoinInVR) {
    options.push({
      title: 'Join in VR',
      href: joinInVRLink,
      onClick: onJoinInVRClick,
    });
  }

  const handleJoinMeetingClick = () => {
    options[0].onClick();
    if (options[0].href != null) {
      window.location.href = options[0].href;
    }
  };

  const handleMenuItemClick = (event, index) => {
    options[index].onClick();
    if (options[index].href != null) {
      window.location.href = options[index].href;
    }
    setSelectedIndex(index);
    setOpen(false);
  };

  const handleToggle = () => {
    setOpen(prevOpen => !prevOpen);
  };

  const handleClose = event => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }
    setOpen(false);
  };

  return (
    <React.Fragment>
      {isRevamp ? (
        <StyledButtonGroup ref={anchorRef}>
          <StyledButton onClick={handleJoinMeetingClick}>
            <StyledButtonImage
              src="video_cam_filled.svg"
              alt="Video Cam Icon"
            />
            <StyledButtonText>Join</StyledButtonText>
          </StyledButton>
          <StyledSecondaryButton onClick={handleToggle}>
            <StyledButtonImage src="arrow_dropdown.svg" alt="Video Cam Icon" />
          </StyledSecondaryButton>
        </StyledButtonGroup>
      ) : (
        <ButtonGroup
          variant="contained"
          size="small"
          ref={anchorRef}
          aria-label="join meeting button"
        >
          <Button onClick={handleJoinMeetingClick} startIcon={<VideocamIcon />}>
            {options[0].title}
          </Button>
          <Button
            size="small"
            aria-controls={open ? 'split-button-menu' : undefined}
            aria-expanded={open ? 'true' : undefined}
            aria-label="select channel to join meeting"
            aria-haspopup="menu"
            onClick={handleToggle}
            sx={{
              width: '40px',
            }}
          >
            <ArrowDropDownIcon />
          </Button>
        </ButtonGroup>
      )}
      <Popper
        sx={{
          zIndex: 1,
        }}
        open={open}
        anchorEl={anchorRef.current}
        role={undefined}
        transition
        disablePortal
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        {({TransitionProps, placement}) => (
          <Grow
            {...TransitionProps}
            style={{
              transformOrigin: {vertical: 'top', horizontal: 'left'},
            }}
          >
            <Paper
              sx={{
                borderRadius: '14px',
              }}
            >
              <ClickAwayListener onClickAway={handleClose}>
                <MenuList
                  id="split-button-menu"
                  autoFocusItem
                  sx={{
                    backgroundColor: 'var(--popover-background-color)',
                    borderRadius: '12px',
                    color: 'var(--primary-color)',
                    padding: '4px',
                    gap: '2px',
                    fontFamily: 'SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif',
                    '& .MuiMenuItem-root:hover': {
                      backgroundColor: 'var(--hover-overlay-color)',
                    },
                    '& .MuiMenuItem-root:active': {
                      backgroundColor: 'var(--focus-overlay-color)',
                    },
                  }}
                >
                  {options.map((option, index) => (
                    <MenuItem
                      key={option.title}
                      onClick={event => handleMenuItemClick(event, index)}
                      dense={true}
                      sx={{
                        borderRadius: '8px',
                        padding: '8px',
                      }}
                    >
                      {option.title}
                    </MenuItem>
                  ))}
                </MenuList>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
    </React.Fragment>
  );
}
