import {useEffect, useState} from 'react';
import genCurrentUserUncached from './genCurrentUser.js';

const THROTTLE_INTERVAL_MILLISECONDS = 5000; // 5 seconds

// Check if browser is connected to some network and check if internalfb.com is reachable
// If internalfb.com is reachable, send a GraphQL request to get user.
// If the GraphQL request fails, then intern is considered down.
export default function useIsInternDown() {
  const [isInternDown, setIsInternDown] = useState();

  const checkOnlineStatus = () => {
    if (!navigator.onLine) {
      setIsInternDown(true);
      return;
    }

    // If the tab is not visible, don't check
    if (document.hidden) {
      return;
    }
    fetch('https://www.internalfb.com/status.php', {
      credentials: 'omit',
      cache: 'no-cache',
    })
      .then(res => {
        if (res.ok) {
          return genCurrentUserUncached();
        }
        throw new Error('Intern is down');
      })
      .then(user => {
        setIsInternDown(!user);
      })
      .catch(() => setIsInternDown(true));
  };

  useEffect(() => {
    const interval = setInterval(() => {
      checkOnlineStatus();
    }, THROTTLE_INTERVAL_MILLISECONDS);
    return () => clearInterval(interval);
  }, []);

  return isInternDown;
}
