import React from 'react';
import CircularProgress from '@mui/material/CircularProgress';
import styled from 'styled-components';
import useGK from './useGK.js';

const SpinnerContainer = styled.div`
  display: flex;
  align-items: center;
  margin: 24px 0px;
`;

const SpinnerContainerV2 = styled.div`
  display: flex;
  align-items: center;
  margin: 24px 0px;
`;

export default function Spinner() {
  const isRevamp = useGK('intern_new_tab_revamp');

  if (isRevamp) {
    return (
      <SpinnerContainerV2>
        <CircularProgress
          size="2rem"
          style={{
            margin: 'auto',
          }}
        />
      </SpinnerContainerV2>
    );
  }

  return (
    <SpinnerContainer>
      <CircularProgress
        size="2rem"
        style={{
          margin: 'auto',
        }}
      />
    </SpinnerContainer>
  );
}
