import {uploadFile} from './initPixelcloud.js';
import {storageKeys} from './LocalStorage.js';
import React, {useEffect, useState} from 'react';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';

export default function Pixelcloud() {
  const [showingConfirm, setShowingConfirm] = useState(false);
  const [isUploading, setIsUploading] = useState(false);
  const [file, setFile] = useState(null);
  const [imgDataURL, setImgDataURL] = useState(null);
  const [confirmRequired] = useStateWithSyncStorage(
    storageKeys.pxcConfirm,
    true
  );

  const close = () => {
    setShowingConfirm(false);
    setIsUploading(false);
    setFile(null);
    setImgDataURL(null);
  };

  const getFileFromClipboard = e => {
    const items = e.clipboardData.items;
    if (!items) {
      return;
    }
    const files = Array.from(items)
      .map(item => item.getAsFile())
      .filter(Boolean);
    if (files.length === 0) {
      return;
    }
    return files[0];
  };

  const getFileFromDrop = e => {
    const files = e.dataTransfer.files;
    if (!files || files.length === 0) {
      return;
    }
    e.preventDefault();
    e.stopPropagation();
    return files[0];
  };

  const prevent = e => {
    e.preventDefault();
    e.stopPropagation();
  };

  useEffect(() => {
    const handlePaste = e => {
      const cbFile = getFileFromClipboard(e);
      if (cbFile) {
        if (confirmRequired) {
          setFile(cbFile);
          setShowingConfirm(true);
        } else {
          setIsUploading(true);
          uploadFile(cbFile).then(() => setIsUploading(false));
        }
      }
    };
    const handleDrop = e => {
      const dropFile = getFileFromDrop(e);
      if (confirmRequired) {
        setFile(dropFile);
        setShowingConfirm(true);
      } else {
        setIsUploading(true);
        uploadFile(dropFile).then(() => setIsUploading(false));
      }
    };
    window.addEventListener('paste', handlePaste);
    const root = document.getElementById('root');
    ['dragenter', 'dragover', 'dragleave'].forEach(event => {
      root.addEventListener(event, prevent);
    });
    root.addEventListener('drop', handleDrop);
    return () => {
      window.removeEventListener('paste', handlePaste);
      ['dragenter', 'dragover', 'dragleave'].forEach(event => {
        root.removeEventListener(event, prevent);
      });
      root.removeEventListener('drop', handleDrop);
    };
  }, [confirmRequired]);

  useEffect(() => {
    if (file == null || file.type == null || !file.type.startsWith('image/')) {
      return;
    }
    const fr = new FileReader();
    fr.onload = e => {
      setImgDataURL(e.target.result);
    };
    fr.readAsDataURL(file);
  }, [file]);

  return (
    <div>
      {!confirmRequired && isUploading && (
        <div className="uploadNotice">Uploading...</div>
      )}
      {showingConfirm && (
        <div className="pxcUploadConfirmation">
          <b>Upload to Pixelcloud?</b>
          <hr />
          {!!imgDataURL && (
            <img
              style={{
                width: '50px',
                height: '50px',
                verticalAlign: 'middle',
                marginRight: '8px',
              }}
              src={imgDataURL}
            />
          )}
          {!!file && file.name}
          <hr />
          <button
            disabled={isUploading}
            onClick={() => {
              setImgDataURL(null);
              if (!!file) {
                setIsUploading(true);
                uploadFile(file).then(() => {
                  setShowingConfirm(false);
                  setFile(null);
                  setIsUploading(false);
                });
              }
            }}
          >
            {isUploading && (
              <svg
                className="spinner"
                width="14px"
                height="14.5px"
                version="1.1"
                viewBox="0 0 5.86 5.99"
                xmlns="http://www.w3.org/2000/svg"
              >
                <g transform="translate(-50.5 -101.4)">
                  <path
                    d="m50.8 104a2.65 2.65 0 0 1 3.02-2.3 2.65 2.65 0 0 1 2.23 3.1 2.65 2.65 0 0 1-3.03 2.3 2.65 2.65 0 0 1-2.22-3.1z"
                    fillOpacity="0"
                    opacity=".996"
                    stroke="#ffffff"
                    strokeDasharray="12.754, 12.754"
                    strokeDashoffset="2.66"
                    strokeWidth=".531"
                  />
                </g>
              </svg>
            )}{' '}
            Upload
          </button>
          <button disabled={isUploading} onClick={() => close()}>
            Cancel
          </button>
        </div>
      )}
    </div>
  );
}
