import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import Settings from './Settings.js';
import SettingsV2 from './SettingsV2.js';
import genLeftNav from './genLeftNav.js';
import Tippy from '@tippyjs/react';
import NewTabPapercuts from './NewTabPapercuts';
import Bugnub from './Bugnub';
import genLog from './genLog.js';
import useGK from './useGK.js';
import ThemedTooltip from './ThemedTooltip.js';

const NavBar = styled.nav`
  display: flex;
  flex-direction: column;
  grid-column: 1;
  grid-row: 1 / 4;
  .searchFocused & {
    display: none;
  }
`;

const NavBarV2 = styled.nav`
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 0;
  top: 52px;
  height: calc(100vh - 52px);
  width: max-content;
  z-index: 2;
  .bookmarksEnabled & {
    top: 92px;
    height: calc(100vh - 92px);
  }
`;

const NavBarList = styled.ul`
  list-style-type: none;
  margin: 0px;
  padding: 0px;
`;
const NavBarListItem = styled.li`
  margin: 0px;
  padding: 0px;
`;

const NavBarItem = styled.a`
  display: flex;
  padding: 10px;
  margin: 4px 8px;
  opacity: 1;
  &:hover {
    opacity: 1;
    background-color: var(--overlay-background);
  }
  background-color: var(--overlay-background-opaque);
  border-radius: 10%;
`;

const NavBarItemImage = styled.img`
  width: 18px;
  height: 18px;
  filter: var(--black-icon-filter);
`;

const NavBarItemV2 = styled.a`
  font-family: SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif;
  align-items: center;
  border-radius: 6px;
  color: var(--primary-color);
  cursor: pointer;
  display: flex;
  font-size: 14px;
  gap: 8px;
  height: 20px;
  margin: 2px 8px;
  opacity: 1;
  padding: 8px;
  &:hover {
    background-color: var(--hover-overlay-color);
    text-decoration: none;
  }
  &:active {
    background-color: var(--focus-overlay-color);
    text-decoration: none;
  }
`;

const NavBarItemImageV2 = styled.img`
  width: 16px;
  height: 16px;
  filter: var(--black-icon-filter);
`;

const NavBarBottomItemsContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 8px 8px 16px 8px;
`;

const NavBarBottomItem = styled(NavBarItemV2)`
  align-self: flex-start;
  padding: 8px 10px;
  margin: 0;
`;

const NavBarItemLabel = styled.div`
  @media (max-width: 1220px) {
    display: none;
  }
`;

const Spacer = styled.div`
  flex-grow: 1;
`;

const PAPERCUTS_URL = 'https://www.internalfb.com/papercuts/internal_new_tab';
const REPORT_BUG_URL =
  'https://www.internalfb.com/tasks?p_nav=MY_TASKS&template=1920061201520276';

export default function LeftNav() {
  const [items, setItems] = useState(null);
  const isRevamp = useGK('intern_new_tab_revamp');

  useEffect(() => {
    genLeftNav(setItems);
  }, []);

  if (isRevamp) {
    return (
      <NavBarV2 aria-labelledby="toolsmenulabel" role="navbar">
        {(items ?? []).map(item => (
          <NavBarItemV2
            href={item.uri}
            onClick={() => genLog('click_left_nav_item', item.label)}>
            <NavBarItemImageV2
              src={item.icon}
              aria-current={item.label}
              role="menuitem"
            />
            <NavBarItemLabel>{item.label}</NavBarItemLabel>
          </NavBarItemV2>
        ))}
        <Spacer />
        <NavBarBottomItemsContainer>
          <ThemedTooltip title="Request a feature" placement="right">
            <NavBarBottomItem
              onClick={() => {
                chrome.tabs.create({url: PAPERCUTS_URL});
              }}>
              <NavBarItemImageV2 src="papercuts_icon.png" role="menuitem" />
            </NavBarBottomItem>
          </ThemedTooltip>
          <ThemedTooltip title="Report a bug" placement="right">
            <NavBarBottomItem
              onClick={() => {
                chrome.tabs.create({url: REPORT_BUG_URL});
              }}>
              <NavBarItemImageV2 src="bug_outline.png" role="menuitem" />
            </NavBarBottomItem>
          </ThemedTooltip>
          <SettingsV2 />
        </NavBarBottomItemsContainer>
      </NavBarV2>
    );
  }

  if (items == null || items.length === 0) {
    return <Settings />;
  }

  return (
    <NavBar aria-labelledby="toolsmenulabel" role="navbar">
      <NavBarList aria-labelledby="toolsmenulabel" role="menu">
        {items.map(item => (
          <NavBarListItem aria-current={item.label} role="menuitem">
            <Tippy content={item.label} key={item.label} placement="right">
              <NavBarItem
                href={item.uri}
                onClick={() => genLog('click_left_nav_item', item.label)}>
                <NavBarItemImage
                  src={item.icon}
                  aria-current={item.label}
                  role="menuitem"
                />
              </NavBarItem>
            </Tippy>
          </NavBarListItem>
        ))}
        <NavBarListItem>
          <NewTabPapercuts />
        </NavBarListItem>
        <NavBarListItem>
          <Bugnub />
        </NavBarListItem>
        <NavBarListItem>
          <Settings />
        </NavBarListItem>
      </NavBarList>
    </NavBar>
  );
}
