// Returns all storage key/vals
export function genAll() {
  return new Promise(resolve => chrome.storage.sync.get(null, resolve));
}

export function genv(keys) {
  return new Promise(resolve => chrome.storage.sync.get(keys, resolve));
}

export async function gen(key) {
  const items = await genv([key]);
  return items[key];
}

export function genSetObject(obj) {
  return new Promise(resolve => chrome.storage.sync.set(obj, resolve));
}

export async function genSet(key, value) {
  return await genSetObject({[key]: value});
}

export function genClear(keys) {
  return new Promise(resolve => chrome.storage.sync.remove(keys, resolve));
}
