import React, {memo} from 'react';
import styled from 'styled-components';
import {genCachedGraphQL} from './../GraphQL.js';
import Table from '@mui/material/Table';
import DocumentTable from './DocumentTable.js';
import TableContainer from '@mui/material/TableContainer';
import Paper from '@mui/material/Paper';
import useGK from '../useGK.js';
import Spinner from '../Spinner.js';

import {
  GET_KNOWLEDGE_USER_DOCUMENTS_FILTERED_QUERY_ID,
  getSourceIcon,
} from './libraryRecentlyViewed.utils.js';
import {useLibraryEnabled} from './libraryRecentlyViewed.hooks.js';
import useStateWithSyncStorage from '../useStateWithSyncStorage.js';
import {storageKeys} from '../LocalStorage.js';

const INTERN_PREFIX = 'https://www.internalfb.com/';

const SOURCE_SET_WITHOUT_FIGMA = ['gdocs', 'notes'];

export const CopyLinkIcon = styled.img`
  height: 15px;
  width: 15px;
  filter: var(--icon-filter);
`;

function LibraryActiveDocuments() {
  const [activeDocuments, setActiveDocuments] = React.useState(null);
  const [fastActiveDocuments, setFastActiveDocuments] = React.useState(null);
  const [theme] = useStateWithSyncStorage(storageKeys.theme);
  const [realtimeActiveDocuments, setRealtimeActiveDocuments] =
    React.useState(null);
  const libraryEnabled = useLibraryEnabled();
  const isRevamp = useGK('intern_new_tab_revamp');

  const GRAPHQL_CACHE_TTL = 3000;

  React.useEffect(() => {
    genCachedGraphQL(
      GET_KNOWLEDGE_USER_DOCUMENTS_FILTERED_QUERY_ID,
      {
        callerInfo: {app: 'intern_new_tab', surface: 'active'},
        minInteractTime: '-30 days',
        documentFetchOption: 'fast',
        fetchOption: 'fast',
        sources: SOURCE_SET_WITHOUT_FIGMA,
        action_types: ['create', 'edit', 'modify', 'share'],
        sortType: 'newest',
      },
      GRAPHQL_CACHE_TTL,
      data => {
        const activeDocuments = getActiveDocumentsFromQueryResult(data, theme);
        setFastActiveDocuments(activeDocuments ?? []);
      }
    );
    genCachedGraphQL(
      GET_KNOWLEDGE_USER_DOCUMENTS_FILTERED_QUERY_ID,
      {
        callerInfo: {app: 'intern_new_tab', surface: 'active'},
        minInteractTime: '-14 days',
        documentFetchOption: 'realtime',
        fetchOption: 'realtime',
        sources: SOURCE_SET_WITHOUT_FIGMA,
        action_types: ['create', 'edit', 'modify', 'share'],
        sortType: 'newest',
      },
      GRAPHQL_CACHE_TTL,
      data => {
        const activeDocuments = getActiveDocumentsFromQueryResult(data, theme);
        setRealtimeActiveDocuments(activeDocuments ?? []);
      }
    );
  }, []);

  React.useEffect(() => {
    if (realtimeActiveDocuments != null && fastActiveDocuments != null) {
      const mergedActiveDocuments = mergeActiveDocuments(
        realtimeActiveDocuments,
        fastActiveDocuments
      );
      setActiveDocuments(mergedActiveDocuments);
    }
  }, [realtimeActiveDocuments, fastActiveDocuments]);

  if (libraryEnabled === false) {
    return null;
  }

  if (activeDocuments === null) {
    return <Spinner />;
  }

  return (
    activeDocuments.length > 0 && (
      <div className="libraryListWrapper">
        <TableContainer
          component={Paper}
          sx={
            isRevamp
              ? {
                  background: 'var(--widget-background-color)',
                  boxShadow: 'none',
                }
              : {background: 'var(--overlay-background)'}
          }
        >
          <Table
            aria-label="active documents"
            sx={
              isRevamp
                ? {
                    tableLayout: 'fixed',
                    '& .MuiTableRow-root:hover': {
                      backgroundColor: 'var(--hover-overlay-color)',
                    },
                    '& .MuiTableRow-root:active': {
                      backgroundColor: 'var(--focus-overlay-color)',
                    },
                  }
                : {
                    '& .MuiTableRow-root:hover': {
                      backgroundColor: 'var(--highlight-background)',
                    },
                  }
            }
          >
            <DocumentTable
              documents={activeDocuments}
              referringTab="RECENTLY_ACTIVE_TAB"
            />
          </Table>
        </TableContainer>
      </div>
    )
  );
}

function getActiveDocumentsFromQueryResult(data, theme, extra) {
  const idSet = new Set();
  const userId = data?.xfb_knowledge_data_viewer?.user_id;
  let documents = null;
  if (data?.xfb_knowledge_data_viewer?.activity_documents != null) {
    documents = data?.xfb_knowledge_data_viewer?.activity_documents?.edges?.map(
      libraryDoc => libraryDoc?.node?.document
    );
  } else {
    documents = data?.xfb_knowledge_data_viewer?.user_documents_filtered?.map(
      userDocument => userDocument.document
    );
  }
  const activeDocuments =
    documents
      .filter(doc => doc != null)
      ?.map(doc => {
        const id = doc?.id;
        const source = getSourceIcon(
          doc?.object_id?.source,
          doc?.mimeType,
          theme
        );
        const url =
          source === 'KNOWLEDGE_BASE' ? INTERN_PREFIX + doc?.url : doc?.url;

        if (idSet.has(id)) {
          return null;
        }
        idSet.add(id);

        return {
          id: doc?.id,
          userId,
          title: doc?.title,
          viewedTime: doc?.viewed_time,
          url,
          source,
          nativeId: doc?.object_id?.native_id,
          modifiedBy: doc?.last_modified_by,
          modifiedTime: doc?.last_modified_time,
          comments: doc?.comments,
          mentions: doc?.mentions,
          mentionsInComments: doc?.mentions_in_comments,
          docSource: doc?.object_id?.source,
        };
      })
      .filter(
        viewedDoc =>
          viewedDoc != null &&
          viewedDoc.title != null &&
          viewedDoc.url != null &&
          viewedDoc.url.trim() !== '' &&
          viewedDoc.modifiedTime != null
      )
      .sort((a, b) => {
        return b.modifiedTime - a.modifiedTime;
      }) ?? [];
  return activeDocuments;
}

function mergeActiveDocuments(
  priorityActiveDocuments,
  secondaryActiveDocuments
) {
  const activeDocuments = [];
  const idSet = new Set();
  for (const doc of priorityActiveDocuments ?? []) {
    const id = doc?.id;
    if (doc.id != null) {
      idSet.add(id);
    }
    activeDocuments.push(doc);
  }

  for (const doc of secondaryActiveDocuments ?? []) {
    const id = doc?.id;
    if (doc.id != null && !idSet.has(id)) {
      activeDocuments.push(doc);
    }
  }
  return activeDocuments.sort((a, b) => {
    return b.modifiedTime - a.modifiedTime;
  });
}

export default memo(LibraryActiveDocuments);
