import React from 'react';
import Event from './Event.js';
import {getLeftPercent} from './calendarPosition.js';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys, DEFAULT_24_HOUR} from './LocalStorage.js';
import classNames from 'classnames';
import styled, {css} from 'styled-components';
import Tippy, {useSingleton} from '@tippyjs/react';
import getDayLabel from './getDayLabel.js';
import {InternNewTabMeetingIDsContext} from './contexts/InternNewTabMeetingIDsContext.react';
import useGK from './useGK.js';
import ThemedTooltip from './ThemedTooltip.js';

const CalendarDiv = styled.div`
  position: relative;
  min-width: 640px;
  height: 90px;
  ${props =>
    props.fullDay
      ? css`
          width: calc(100vw - 800px);
          min-width: 850px;
        `
      : css`
          width: 640px;
        `};
`;

const CalendarDivV2 = styled.div`
  position: relative;
  max-width: 960px;
  height: 94px;
  width: 100%;
`;

const CustomDayDiv = styled.div`
  position: absolute;
  top: -20px;
  right: -36px;
  color: var(--primary-color);
`;

const StatusOverlay = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  opacity: 0.5;
  border-radius: 8px;
  ${props =>
    props.availability === 'OFF_THE_GRID'
      ? css`
          background-color: #ea817f;
        `
      : css`
          background-color: #fef2d1;
        `}
`;

const CalendarArrowImage = styled.img`
  height: 16px;
  width: 16px;
  filter: var(--primary-icon-filter);
  vertical-align: middle;
`;

function Hour(props) {
  const [use24Hour] = useStateWithSyncStorage(
    storageKeys.use24Hour,
    DEFAULT_24_HOUR
  );
  let hour = props.hour;
  if (hour != null) {
    if (use24Hour) {
      if (hour < 10) {
        hour = '0' + hour;
      }
      hour = hour + ':00';
    } else if (hour === 0) {
      hour = '12AM';
    } else if (hour === 12) {
      hour = '12PM';
    } else if (hour > 12) {
      hour = hour - 12 + 'PM';
    } else {
      hour = hour + 'AM';
    }
  }

  if (props.isRevamp) {
    return (
      <div className="hourCell">
        {!props.isFirstShownHour && <div className="divider" />}
        {hour != null ? (
          <div className="hourV2 secondaryTextColor">{hour}</div>
        ) : null}
      </div>
    );
  }

  return (
    <div className="hourCell">
      <div className="divider" />
      {hour != null ? (
        <div
          className={classNames({
            hour: true,
            primaryColor: props.isFuture,
            secondaryColor: !props.isFuture,
          })}>
          {hour}
        </div>
      ) : null}
    </div>
  );
}

function CurrentTime(props) {
  const isRevamp = useGK('intern_new_tab_revamp');
  const left = getLeftPercent(props.now, props.fullDay);
  if (left <= 0 || left >= 100) {
    return null;
  }
  return (
    <>
      <div
        className={isRevamp ? 'currentTimeBallV2' : 'currentTimeBall'}
        style={{left: left + '%'}}
      />
      <div
        className={isRevamp ? 'currentTimeV2' : 'currentTime'}
        style={{left: left + '%'}}
      />
    </>
  );
}

function eventsOverlap(e1, e2) {
  return e1.end_time > e2.start_time && e1.start_time < e2.end_time;
}

function _packEvents(columns, tippySingleton) {
  const events = [];
  columns.forEach((col, row) => {
    col.forEach(event => {
      events.push(
        <Event
          key={event.id}
          event={event}
          top={(row / columns.length) * 45 + (row > 0 ? 1 : 0)}
          height={45 / columns.length - (row < columns.length - 1 ? 1 : 0)}
          tippySingleton={tippySingleton}
        />
      );
    });
  });
  return events;
}

function getHours(calendarFullDay, curhour, isRevamp) {
  const hours = [];
  const startHour = calendarFullDay ? 0 : 7;
  const endHour = calendarFullDay ? 24 : 19;
  if (isRevamp) {
    if (calendarFullDay) {
      const startHour = 0;
      const endHour = 24;

      for (let hour = startHour; hour < endHour; hour += 2) {
        hours.push(
          <Hour
            isFirstShownHour={startHour === hour}
            key={hour}
            hour={hour}
            isFuture={hour >= curhour}
            isRevamp={isRevamp}
          />
        );
      }

      return hours;
    }
  }

  for (let hour = startHour; hour < endHour; ++hour) {
    hours.push(
      <Hour
        isFirstShownHour={startHour === hour}
        key={hour}
        hour={hour}
        isFuture={hour >= curhour}
        isRevamp={isRevamp}
      />
    );
  }

  return hours;
}

function Calendar(props) {
  const isRevamp = useGK('intern_new_tab_revamp');
  const [source, target] = useSingleton();
  const [calendarEnabled] = useStateWithSyncStorage(storageKeys.calendar, true);
  const [calendarFullDay] = useStateWithSyncStorage(
    storageKeys.calendarFullDay,
    false
  );
  if (!calendarEnabled) {
    return null;
  }

  const now = props.now;
  const curhour = now.getHours();
  const hours = getHours(calendarFullDay, curhour, isRevamp);

  const meeting_ids = props.events.map(event => event.id);
  let events = [];
  let lastEventEnd = null;
  let columns = [];
  for (let i = 0; i < props.events.length; ++i) {
    const event = props.events[i];
    if (lastEventEnd != null && event.start_time >= lastEventEnd) {
      events = events.concat(_packEvents(columns, target));
      columns = [];
      lastEventEnd = null;
    }
    let placed = false;
    for (let j = 0; j < columns.length; ++j) {
      const col = columns[j];
      if (!eventsOverlap(col[col.length - 1], event)) {
        columns[j].push(event);
        placed = true;
        break;
      }
    }
    if (!placed) {
      columns.push([event]);
    }
    if (lastEventEnd == null || event.end_time > lastEventEnd) {
      lastEventEnd = event.end_time;
    }
  }
  if (columns.length > 0) {
    events = events.concat(_packEvents(columns, target));
  }
  const status = props.status;
  const availability = status?.availability;

  const CalendarDivComponent = isRevamp ? CalendarDivV2 : CalendarDiv;

  return (
    <InternNewTabMeetingIDsContext.Provider value={meeting_ids}>
      <Tippy
        singleton={source}
        className="tippyPopover"
        interactive={true}
        offset={[0, 0]}
        appendTo={document.body}
        placement="bottom"
        arrow={false}
      />
      <div className={isRevamp ? 'calendarWrapperV2' : 'calendarWrapper'}>
        <CalendarDivComponent fullDay={calendarFullDay}>
          <div
            className={
              isRevamp
                ? 'calendarWashV2 calendarWashBackground'
                : 'calendarWash overlayBackground'
            }
          />
          {isRevamp ? (
            <>
              <ThemedTooltip title="Previous Day">
                <button
                  className={
                    isRevamp
                      ? 'calendarChevronV2 calendarLeftChevronV2'
                      : 'calendarChevron calendarLeftChevron'
                  }
                  onClick={props.onPrevious}>
                  {isRevamp ? (
                    <CalendarArrowImage
                      src="chevron-left.svg"
                      className="icon"
                      alt="Previous Day"
                    />
                  ) : (
                    <img
                      src="chevron-left.png"
                      className="icon"
                      alt="Previous Day"
                    />
                  )}
                </button>
              </ThemedTooltip>
              <ThemedTooltip title="Next Day">
                <button
                  className={
                    isRevamp
                      ? 'calendarChevronV2 calendarRightChevronV2'
                      : 'calendarChevron calendarRightChevron'
                  }
                  onClick={props.onNext}>
                  {isRevamp ? (
                    <CalendarArrowImage
                      src="chevron-right.svg"
                      className="icon"
                      alt="Next Day"
                    />
                  ) : (
                    <img
                      src="chevron-right.png"
                      className="icon"
                      alt="Next Day"
                    />
                  )}
                </button>
              </ThemedTooltip>
            </>
          ) : (
            <>
              <Tippy content="Previous Day">
                <button
                  className={
                    isRevamp
                      ? 'calendarChevronV2 calendarLeftChevronV2'
                      : 'calendarChevron calendarLeftChevron'
                  }
                  onClick={props.onPrevious}>
                  {isRevamp ? (
                    <CalendarArrowImage
                      src="chevron-left.svg"
                      className="icon"
                      alt="Previous Day"
                    />
                  ) : (
                    <img
                      src="chevron-left.png"
                      className="icon"
                      alt="Previous Day"
                    />
                  )}
                </button>
              </Tippy>
              <Tippy content="Next Day">
                <button
                  className={
                    isRevamp
                      ? 'calendarChevronV2 calendarRightChevronV2'
                      : 'calendarChevron calendarRightChevron'
                  }
                  onClick={props.onNext}>
                  {isRevamp ? (
                    <CalendarArrowImage
                      src="chevron-right.svg"
                      className="icon"
                      alt="Next Day"
                    />
                  ) : (
                    <img
                      src="chevron-right.png"
                      className="icon"
                      alt="Next Day"
                    />
                  )}
                </button>
              </Tippy>
            </>
          )}
          <div className={isRevamp ? 'hourCellsV2' : 'hourCells borderColor'}>
            {hours}
          </div>
          {availability != null && availability !== 'NORMAL' ? (
            <Tippy
              content={`${status.availability_text} until ${status.until_text}${
                status.description ? ': ' + status.description : ''
              }`}>
              <StatusOverlay availability={availability} />
            </Tippy>
          ) : null}
          {events}
          {props.hideCurrentTime ? (
            !isRevamp && (
              <CustomDayDiv>{getDayLabel(props.customNow)}</CustomDayDiv>
            )
          ) : (
            <CurrentTime now={now} fullDay={calendarFullDay} />
          )}
        </CalendarDivComponent>
      </div>
    </InternNewTabMeetingIDsContext.Provider>
  );
}

export default Calendar;
