import {genGraphQL} from './GraphQL.js';
import {storageKeys} from './LocalStorage';

const version = chrome.runtime.getManifest().version;

export default function genLog(event, action) {
  const session_id = window.sessionStorage.getItem(storageKeys.sessionId);
  genGraphQL(
    '1942031559258123',
    action ? {event, action, version, session_id} : {event, version, session_id}
  );
}
