import {genGraphQL} from '../GraphQL';

export const INTERN_PREFIX = 'https://www.internalfb.com/';
// These IDs are unique to the Internal New Tab
export const PERSISTED_QUERY_ID = '7842742735795163';
export const PERSISTED_LOGGER_QUERY = '5478423042222404';
export const GET_ACTIVE_DOCUMENTS_FAST_QUERY_ID = '9751109628240143';
export const GET_KNOWLEDGE_USER_DOCUMENTS_FILTERED_QUERY_ID =
  '9012751465461509';
// THIS IS SHARED WITH ALWAYS-ON-KNOWLEDGE!
const ADD_TO_COLLECTION_QUERY = '5246749685383132';
const GET_COLLECTIONS_QUERY = '7327711020604233';
const GET_COLLECTION_DETAILS_QUERY = '4837915839660636';

export const _genLog = (event, payload) => {
  genGraphQL(PERSISTED_LOGGER_QUERY, {
    event,
    ...payload,
  });
};

export const _saveToCollection = (rv, collectionId) =>
  genGraphQL(ADD_TO_COLLECTION_QUERY, {
    name: rv.title,
    parentCollectionID: collectionId,
    url: rv.url,
  });

export const getSourceIcon = (source, type, theme = 'light') => {
  if (source.toUpperCase() === 'GDOCS') {
    switch (type) {
      case 'SPREADSHEET':
        return 'sheet.png';
      case 'SLIDESHOW':
        return 'slide.png';
      case 'DOCUMENT':
        return 'doc.png';
      default:
      // continue to second switch using source
    }
  }

  switch (source.toUpperCase()) {
    case 'WIKI':
      return `/wiki_filled_${theme}.png`;
    case 'NOTES':
      return '/notes.png';
    case 'GDOCS':
      return '/gdrive.png';
    default:
      return `/wiki_filled_${theme}.png`;
  }
};

export const getCollections = () =>
  genGraphQL(GET_COLLECTIONS_QUERY, null).then(data => {
    const collections = data?.xfb_knowledge_hub_viewer_root_collection.nodes
      .filter(col => col.id != null)
      .filter(col => col.collection_type !== 'PINNED')
      .sort((col1, col2) => (col1.creation_time < col2.creation_time ? 1 : -1));
    return collections;
  });

export const getRecentlyViewed = (theme) =>
  genGraphQL(PERSISTED_QUERY_ID, null).then(data => {
    const idSet = new Set();
    const userId = data?.xfb_knowledge_data_viewer?.user_id;
    const viewedDocuments =
      data?.xfb_knowledge_data_viewer?.recent_documents
        ?.map(libraryDoc => {
          const id = libraryDoc.document.id;
          const source = getSourceIcon(
            libraryDoc?.document?.object_id?.source,
            libraryDoc?.document?.type,
            theme,
          );
          if (source.toUpperCase() === 'FIGMA') {
            return null;
          }
          // TODO: dedupe and add prefix on backend
          const url =
            source === 'KNOWLEDGE_BASE'
              ? INTERN_PREFIX + libraryDoc?.document?.url
              : libraryDoc?.document?.url;

          if (idSet.has(id)) {
            return null;
          }
          idSet.add(id);

          const doc = libraryDoc?.document;
          return {
            id: doc?.id,
            userId,
            title: libraryDoc?.document?.title ?? 'Untitled',
            viewedTime: doc?.viewed_time,
            url,
            source,
            nativeId: doc?.object_id?.native_id,
            modifiedBy: doc?.last_modified_by,
            modifiedTime: doc?.last_modified_time,
            mentions: doc?.mentions,
          };
        })
        .filter(viewedDoc => viewedDoc != null)
        .sort((a, b) => {
          return b.viewedTime - a.viewedTime;
        }) ?? [];
    return viewedDocuments;
  });

const getObjectSource = objectLink => {
  if (objectLink == null) {
    return null;
  }
  return objectLink.split(':')[0];
};

export const getCollectionDetails = collectionId =>
  genGraphQL(GET_COLLECTION_DETAILS_QUERY, {collectionId}).then(data => {
    const documents = data?.collection?.objectLinkNodes
      ? data?.collection?.objectLinkNodes
          .filter(o => o != null)
          .map(node => ({
            id: node.id,
            title: node.name,
            time: node.creation_time,
            url: node.url_link,
            source:
              node.knowledge_document?.object_id?.source ??
              getObjectSource(node.object_link),
            type: node.knowledge_document?.type,
            nativeId: node.knowledge_document?.object_id?.native_id ?? node.id,
          }))
      : [];
    return documents;
  });

export const getExistingCollectionItems = async collectionId => {
  const items = new Set();
  const details = await getCollectionDetails(collectionId);
  if (!details) {
    return items;
  }

  details.forEach(item => {
    items.add(item.nativeId);
  });

  return items;
};

export const getActiveDocuments = (theme) =>
  genGraphQL(GET_ACTIVE_DOCUMENTS_FAST_QUERY_ID, null).then(data => {
    const idSet = new Set();
    const activeDocuments =
      data?.xfb_knowledge_data_viewer?.activity_documents?.edges
        ?.map(libraryDoc => {
          const doc = libraryDoc?.node?.document;
          const id = doc.id;
          const source = getSourceIcon(doc?.object_id?.source, doc?.type, theme);
          // TODO: dedupe and add prefix on backend
          const url =
            source === 'KNOWLEDGE_BASE' ? INTERN_PREFIX + doc?.url : doc?.url;

          if (idSet.has(id)) {
            return null;
          }
          idSet.add(id);

          return {
            id: doc?.id,
            title: doc?.title,
            time: doc?.time_interacted,
            url,
            source,
            nativeId: doc?.object_id?.native_id,
          };
        })
        .filter(viewedDoc => viewedDoc != null)
        .sort((a, b) => {
          return b.time - a.time;
        }) ?? [];
    return activeDocuments;
  });
