import Timestamp from './Timestamp.js';
import React from 'react';
import getInternURL from './getInternURL.js';
import JoinMeetingButton from './JoinMeetingButton.js';
import OneOneButton from './OneOneButton.js';
import Tippy from '@tippyjs/react';
import Linkify from 'react-linkify';
import CalendarKnowledgeLinks from './CalendarKnowledgeLinks.js';
import genLog from './genLog.js';

function EventPopover(props) {
  const start = new Date(props.event.start_time * 1000);
  const end = new Date(props.event.end_time * 1000);
  const inviteeCount = props.event.attendees.count;
  const invitees = props.event.attendees.edges.map((a, idx) => {
    const pic = a.node.profile_picture
      ? a.node.profile_picture.uri
      : 'silhouette.png';
    const id = a.node.employee ? a.node.employee.unencoded_id : null;
    if (id != null) {
      return (
        <Tippy content={a.node.employee.full_name} placement="top" key={id}>
          <a
            className="eventPopoverAttendee"
            href={getInternURL('/profile/?id=' + id)}
          >
            <img src={pic} alt="Event Attendee" />
          </a>
        </Tippy>
      );
    }
    return (
      <Tippy content={a.node.email} placement="top" key={idx}>
        <div className="eventPopoverAttendee">
          <img src={pic} alt="Event Attendee" />
        </div>
      </Tippy>
    );
  });
  invitees.reverse();
  const uri = props.event.uri || props.event.edit_uri;
  const description = props.event.notes_rte?.rte_content_plain_text;
  return (
    <div className="eventPopover overlayBackground primaryColor">
      <div className="eventPopoverTitle">
        {uri != null ? (
          <a href={uri} onClick={() => genLog('click_individual_meeting')}>
            {props.event.title}
          </a>
        ) : (
          props.event.title
        )}
      </div>
      <div className="eventPopoverTime">
        <Timestamp time={start} />
        {' - '}
        <Timestamp time={end} />
      </div>
      {description ? (
        <div className="eventPopoverDescription">
          <Linkify>{description}</Linkify>
        </div>
      ) : null}
      <div className="eventPopoverLocation">
        {(props.event.rooms || []).map((r, i) => (
          <div key={'room' + i}>
            <a
              href={getInternURL('/room_profile/?id=' + r.room.id)}
              onClick={() => genLog('click_meeting_location')}
            >
              {r.display_name}
            </a>
          </div>
        ))}
        {(props.event.freeform_locations || []).map((f, idx) => (
          <div key={idx}>{f}</div>
        ))}
      </div>
      <CalendarKnowledgeLinks event={props.event} />
      {inviteeCount > 0 ? (
        <div className="eventPopoverAttendees">
          {invitees.length < inviteeCount ? (
            <div className="eventPopoverAttendee eventPopoverAttendeeExtra">
              {'+' + String(inviteeCount - invitees.length)}
            </div>
          ) : null}
          {invitees}
        </div>
      ) : null}
      <JoinMeetingButton event={props.event} />
      <OneOneButton event={props.event} />
    </div>
  );
}

export default EventPopover;
