import React from 'react';

const METAMATE_EXTENSION_ID = 'ihhacjmejhjflegnlfpigefiolhppmli';

export default function useMetamate() {
  const [hasMetamate, setHasMetamate] = React.useState(false);

  React.useEffect(() => {
    chrome.runtime.sendMessage(
      METAMATE_EXTENSION_ID,
      {action: 'IsInstalled'},
      {},
      response => {
        if (response === true) {
          setHasMetamate(true);
        } else {
          // ignore the error
          const _ = chrome.runtime.lastError;
        }
      }
    );
  }, []);

  const openMetamate = React.useCallback(() => {
    chrome.tabs.getCurrent(tab => {
      chrome.runtime.sendMessage(
        METAMATE_EXTENSION_ID,
        {action: 'OpenMetamate', tabId: tab.id},
        {},
        () => {
          // ignore the error
          const _ = chrome.runtime.lastError;
        }
      );
    });
  }, []);

  return {hasMetamate, openMetamate};
}
